<template>
    <div>
      <el-dialog
            title="新增代理地址"
            :visible.sync="proxyDialog"
            class="show"
            width="30%">
            <el-form :inline="true" :model="proxyAddressConfigList" ref="emailConfigList" :rules="rules" :label-position="labelPosition" label-width="80px" size="small">
                <el-form-item label="所属项目" prop="emailTeam">
                    <el-select filterable style="width:250px" v-model="proxyAddressConfigList.Modelversion_id">
                        <el-option
                                    v-for="item in versionList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="代理地址" prop="proxyAddress">
                    <el-input style="width:250px"   v-model="proxyAddressConfigList.pro_address"  >
                    </el-input>
                </el-form-item>
                <el-form-item label="企信群ID" prop="pro_qixin">
                      <el-input style="width:250px" :rows="3"  type="textarea"  v-model="proxyAddressConfigList.pro_qixin"  >
                      </el-input>
                      <div style="font-size: xx-small">【注意】看黑板！！！多个企信群需要以逗号分隔</div>
                  </el-form-item>
              <el-form-item label="开启巡检" prop="openXunjian">
                  <el-switch
                    v-model="proxyAddressConfigList.openXunjian"
                    active-value=true
                    inactive-value=false
                  >
                  </el-switch>

              </el-form-item>
              <el-form-item label="企信通知" prop="openQiXin">
                  <el-switch
                    v-model="proxyAddressConfigList.openQiXin"
                    active-value=true
                    inactive-value=false
                  >
                  </el-switch>

              </el-form-item>
            </el-form>
              <div class="dialog-foot">
<!--                  <span slot="footer" class="dialog-footer">-->
                  <el-button type="primary" @click="proxyDialog=false" size="mini" >取 消</el-button>
                  <el-button type="primary" size="mini" @click="addAddress(proxyAddressConfigList)" >确 定</el-button>
<!--                </span>-->
                </div>

        </el-dialog>
    </div>
</template>
<style scoped>
.el-dialog__footer{
    text-align: center;
}
.el-form-item{
    margin-bottom: 20px;
}
</style>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
            labelPosition:'left',
            proxyDialog:false,
            freshAddress:true,
            openXunjian:false,
            versionList:[],
            proxyAddressConfigList:{
                proxyid:"",
                Modelversion_id:'',
                pro_address:'',
                openXunjian:false,
                pro_qixin:'',
                openQiXin:false,
            },
            rules:{
                version:[
                    {required:true,message:'请选择项目',trigger: 'change'}
                ],
                proxyAddress:[
                    {required:true,message:'请选择代理服务器地址',tigger:'blur'}
                ]
            }
        }
    },
    methods:{
      //是否开启巡检
      changeXunJianStyle(){
        this.proxyAddressConfigList.openXunjian=this.openXunjian
        console.log('openXunjian',this.proxyAddressConfigList.openXunjian)
      },
      //清除代理地址
      clearAddress(){
        this.proxyAddressConfigList={
                proxyid:"",
                Modelversion_id:'',
                pro_address:'',
            }
      },
      //新增代理地址
      addAddress(val){
        console.log(val)

        this.$axios.post(this.$api.saveAddProAddress,qs.stringify(val))
          .then(response=>{
            if(response.data.code==200){
                        this.$message({
                            showClose:true,
                            message:response.data.msg,
                            type:"success"
                        })
                        bus.$emit('freshAddress',this.freshAddress)
                        // this.selectVersionInfomation()
                    }else{
                        this.$message({
                            showClose:true,
                            message:"Fail",
                            type:"fail"
                        })
                    }
            this.proxyDialog=false
          })
      },
        //重置邮件配置
        // resetForm(emailConfigList){
        //     console.log("重置")
        // },
        //邮件配置-删除
        // delConfig(val){
        //     this.emailConfigList=val
        //     this.emailConfigList.action='del'
        //     this.emailConfigList.emailId=val.email_id
        //     this.$axios.post(this.$api.emailConfig,qs.stringify(this.emailConfigList))
        //         .then(response=>{
        //             console.log(response.data)
        //             if(response.data.code==0){
        //                 this.$message({
        //                     showClose:true,
        //                     message:response.data.msg,
        //                     type:"success"
        //                 })
        //                 bus.$emit('freshTable',this.freshTable)
        //             }else{
        //                 this.$message({
        //                     showClose:true,
        //                     message:"Fail",
        //                     type:"fail"
        //                 })
        //             }
        //         })
        // },
        //邮件配置-新增-编辑
        // emailConfig(emailConfigList){
        //         this.$refs[emailConfigList].validate((valid)=>{
        //             if (valid){
        //                 console.log('修改前email配置',this.emailConfigList)
        //                 if(this.emailConfigList.emailId==''){
        //                     this.emailConfigList.action='add'
        //                 }else{
        //                     this.emailConfigList.action='edit'
        //                 }
        //                this.$axios.post(this.$api.emailConfig,qs.stringify(this.emailConfigList))
        //                .then(response=>{
        //                    console.log(response.data)
        //                    if(response.data.code==0){
        //                         this.$message({
        //                             showClose:true,
        //                             message:response.data.msg,
        //                             type:"success"
        //                         })
        //                         bus.$emit('freshTable',this.freshTable)
        //                     }else{
        //                         this.$message({
        //                             showClose:true,
        //                             message:"Fail",
        //                             type:"fail"
        //                         })
        //                     }
        //                     this.emailDialog=false
        //                })
        //             } else {
        //                 console.log('error submit!!');
        //                 return false;
        //             }
        //
        //         })
        // },
        //获取版本信息
        selectVersionInfomation(){
            this.$axios.get(this.$api.selectVersionData)
            .then(response=>{
                this.versionList=response.data
            })
        }
    },
    mounted(){
        
    },
    created(){
        this.selectVersionInfomation();
        bus.$on('openProxy',msg=>{
            this.proxyDialog=true
          this.clearAddress()
        })
        bus.$on('openAdd',msg=>{
            this.proxyDialog=true
            this.proxyAddressConfigList.proxyid=msg.proxyid
            this.proxyAddressConfigList.Modelversion_id=msg.Modelversion_id_id
            this.proxyAddressConfigList.pro_address=msg.pro_address
            this.proxyAddressConfigList.openXunjian=msg.pro_openXunjian
            this.proxyAddressConfigList.pro_qixin=msg.pro_qixin
            this.proxyAddressConfigList.openQiXin=msg.pro_openQiXin
        })
    }
}
</script>
<style scoped>
.show  /deep/.el-dialog__headerbtn{
        position: absolute;
        top: 5px ;
        right: 20px;
        padding: 0;
        background: 0 0;
        border: none;
        outline: 0;
        cursor: pointer;
        font-size: 16px;
  }
.dialog-foot{
        text-align: center;
}
</style>



