<template>
    <div class="tags">
        <ul style="margin: 1px;padding-left: 2px">
            <li class="tags-li " v-for="(item,index) in taglist" :key="index"
            :class="{'active': isActive(item.path)}"
             >
                <router-link :to="item.path" class="tags-li-title"  >
                    {{item.title}}
                    <span class="tags-li-icon" @click="handleClose(index)"><i class="el-icon-close"></i></span>
                </router-link>
            </li>
        </ul>
        <v-contextmenu ref="contextmenu">   
            <v-contextmenu-item @click="refreshE">刷新</v-contextmenu-item>
        </v-contextmenu>
        <!-- <el-tag
        :key="index"
        v-for="(item,index) in taglist"
        closable
        size="medium"
        :disable-transitions="false"
        @close="handleClose(item)">
        <router-link :to="item.path">
        {{item.title}}
        </router-link>
        </el-tag>  -->
    </div>
</template>
<style>
    .tags{
        /* float: left; */
        height: 35px;
        overflow: hidden;
        background: #fff;
        box-shadow: 0px 0px 4px #5497e0 inset;
    }
    .tags ul {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
    }
    .tags-li{
        float: left;
        margin: 3px 5px 2px 3px;
        border-radius: 3px;
        font-size: 12px;
        overflow: hidden;
        cursor: pointer;
        height: 23px;
        line-height: 23px;
        border: 1px solid #e9eaec;
        background: #fff;
        padding: 0 5px 0 12px;
        vertical-align: middle;
        color: #666;
        -webkit-transition: all .1s ease-in;
        -moz-transition: all .1s ease-in;
        transition: all .1s ease-in;
    }
    .tags-li-title {
        text-decoration:none;
        /* float: left; */
        max-width: 80px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin-right: 5px;
        color: #666;
    }
    .tags-li.active .tags-li-title {
        color: #fff;
    }
    .tags-li.active {
        border: 1px solid #26ccc2;
        background-color: #26ccc2;
    }
    /* .tags-close-box {
        position: absolute;
        right: 0;
        top: 0;
        box-sizing: border-box;
        padding-top: 1px;
        text-align: center;
        width: 110px;
        height: 35px;
        background: #fff;
        box-shadow: -3px 0 15px 3px rgba(0, 0, 0, .1);
        z-index: 10;
    } */
</style>
<script>
import bus from '../bus';
export default {
    data(){
        return{
            dynamicTags: ['标签一', '标签二', '标签三'],
            taglist:[],
            tempItem:''
        }
    },
    methods:{
        //关闭单个标签
        getItem(item){
            this.tempItem=item;
        },
        refreshE(){
                // this.tagsList.splice(index, 1);
                bus.$emit('refreshE', this.tempItem);
                // console.log("刷新项目管理")
            },
        isActive(path){
            return path===this.$route.fullPath;
        },
        handleClose(index){
        // this.taglist.splice(this.taglist.indexOf(index), 1);
        const delItem = this.taglist.splice(index, 1);
        const item = this.taglist[index] ? this.taglist[index] : this.taglist[index - 1];
        if (item) {
            console.log("delItem",delItem.path)
            console.log("fullPath",this.$route.fullPath)
            delItem.path === this.$route.fullPath && this.$router.push(item.path);
        } else {
            this.$router.push('/');
        }
      },
      //设置标签
      setTags(route){
        //   console.log('获取的route全路径',route.fullPath);
          const isExist=this.taglist.some(item=>{
              return item.path===route.fullPath
          }
          )
           if(!isExist){
              if(this.taglist.length>=8){
                  this.taglist.shift();
              }
              this.taglist.push({
                title:route.meta.title,
                path:route.fullPath,
                name: route.matched[1].components.Manage.name
              }
                
              )
          }
        //   console.log('获取到的taglist是',this.taglist);
          bus.$emit('tags', this.taglist);
      }
    },
    watch:{
        $route(newValue){
            this.setTags(newValue)
        }
    },
    created(){
        // console.log(this.$route);
        this.setTags(this.$route)
        bus.$on('close_current_tags',()=>{
            for(let i=0,len=this.taglist.length;i<len;i++){
                const item=this.taglist[i];
                if(item.path===this.$route.fullPath){
                    if(i<len-1){//
                        this.$route.push(this.taglist[i+1].path)
                    } else if (i > 0) {
                            this.$router.push(this.tagsList[i - 1].path);
                    } else {
                        this.$router.push('/');
                    }
                }
            }
        })
    }
    // computed:{
    //     showTags
    // }

}
</script>


