<template>
    <div class="main-type">
        <el-row>
        <!-- <div class="search-style"> -->
            <el-form :inline="true" size="small" class="demo-form-inline search-style">
                <!-- <el-form-item label="项目名称">
                    <el-input size="mini" placeholder="请输入项目名称"></el-input>
                </el-form-item> -->
                <el-form-item>
                    <el-button style="margin-left:10px;" size="mini" type="primary" @click="addUITestCase">
                        添加用例
                    </el-button>
                    <el-button size="mini" type="primary" @click="freshScript">
                        添加Script
                    </el-button>
                    <el-button size="mini" type="primary" @click="batExecuting">
                        批量执行
                    </el-button>
                    <el-button size="mini" type="primary" @click="bulkCopy">
                        批量复制
                    </el-button>
                    <el-button size="mini" type="primary" @click="addProxyAddress">
                        新增代理
                    </el-button>
                    <el-button size="mini" type="primary" @click="addEmailConfig">
                        邮件配置
                    </el-button>
                </el-form-item>
            </el-form>
            </el-row>
        <el-tabs class="tabs-style"
        v-model="activeName"
        >
            <el-tab-pane   label="用例" name="first">
                <div class="background-type">
                        <el-row>
                    <el-col :span="4" class="tree-style" >
<!--                        <el-card class="box-card" shadow="hover">-->
                        <uiTree></uiTree>
                        <treeUIDialog></treeUIDialog>
<!--                        </el-card>-->
                    </el-col>
                    <el-col :span="19" class="table-sytle">
                        <el-table
                        :data="testCaseList"
                        :row-key="row_key"
                        stripe
                        :header-cell-style="{background:'#F2F2F2'}"
                        @expand-change="expandChange"
                        @selection-change="handleChange"
                        :expand-row-keys="expendRowKey"
                        >
                            <el-table-column
                            type="selection"
                            width="55">
                            </el-table-column>
                            <el-table-column
                            label="序号"
                            prop="testcase_id"
                            width="55">
                            </el-table-column>
                            <el-table-column type="expand">
                                <template slot-scope="props">
                                    <el-table
                                    @selection-change="handleChangeExpand"
                                    :data="props.row.testcaseList"
                                    width="auto"
                                    :header-cell-style="{background:'#F2F2F2'}"
                                    >
                                        <el-table-column
                                          type="selection"
                                          width="55">
                                        </el-table-column>
                                        <el-table-column prop="script_id" label="ID" width="50">
                                        </el-table-column>
                                        <el-table-column prop="script_testDataName" label="脚本名称">
                                        </el-table-column>
                                        <el-table-column prop="script_testDataKeyWord" label="关键字">
                                            <template slot-scope="scope">
                                            <el-select size="mini" v-model="scope.row.script_testDataKeyWord">
                                                <el-option
                                                    v-for="item in keyList"
                                                    :key="item.value"
                                                    :label="item.label"
                                                    :value="item.value">
                                                </el-option>
                                            </el-select>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="script_testDataPositioning" label="元素定位" width="150">
                                            <template slot-scope="scope">
                                            <el-select size="mini" v-model="scope.row.script_testDataPositioning">
                                                <el-option
                                                    v-for="item in methodList"
                                                    :key="item.value"
                                                    :label="item.label"
                                                    :value="item.value">
                                                </el-option>
                                            </el-select>
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="操作" width="250">
                                            <template slot-scope="scope">
                                                <el-button @click="moveTestCase(scope.row)" type="text" size="mini">移动</el-button>
                                                <el-button @click="copyUiTest(scope.row)" type="text"  size="mini">复制</el-button>
                                                <el-button @click="delUiTest(scope.row)" type="text" size="mini">删除</el-button>
                                                <el-button @click="editUiTest(scope.row)" type="text" size="mini">编辑</el-button>
                                                <i class="el-icon-bottom" @click="handleDown(scope.$index,scope.row)" style="color:#409EFF"></i>
                                                <i class="el-icon-top" @click="handleUp(scope.$index,scope.row)" style="color:#409EFF"></i>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </template>
                            </el-table-column>
                            
                            <el-table-column
                            prop="testcase_caseName"
                            label="用例名称"
                            >
                            </el-table-column>
                            <el-table-column
                            prop="testcase_caseType"
                            label="用例类型"
                            >
                                <template slot-scope="scope">
                                    <div v-if="scope.row.testcase_caseType==1">
                                    <el-tag>
                                        需求测试
                                    </el-tag>
                                    </div>
                                    <div v-if="scope.row.testcase_caseType==2">
                                        <el-tag>
                                        回归测试
                                        </el-tag>
                                        </div>
                                    <div v-if="scope.row.testcase_caseType==3">
                                        <el-tag>
                                        业务自动化
                                        </el-tag>
                                        </div>
                                </template>
                            </el-table-column>
                            <el-table-column
                            prop="testcase_caseLeve"
                            label="用例等级"
                            >
                            <template slot-scope="scope">
                                <div v-if="scope.row.testcase_caseLeve==1">
                                    <el-tag>
                                    主流程
                                    </el-tag>
                                    </div>
                                <div v-if="scope.row.testcase_caseLeve==2">
                                    <el-tag>
                                    例行回归
                                    </el-tag>
                                    </div>
                                <div v-if="scope.row.testcase_caseLeve==3">
                                    <el-tag>
                                    生产回归
                                    </el-tag>
                                    </div>
                                <div v-if="scope.row.testcase_caseLeve==4">
                                    <el-tag>
                                    普通
                                    </el-tag>
                                    </div>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="操作"
                            >
                            <template slot-scope="scope">
                                <el-button size="mini" @click="copyTestsCase(scope.row)"  type="text">复制</el-button>
                                <el-button size="mini" @click="editTestCase(scope.row)"   type="text">编辑</el-button>
                                <el-button size="mini" @click="deleteTestCase(scope.row)" type="text">删除</el-button>
                            </template>
                            </el-table-column>
                        </el-table>
                    </el-col>
                </el-row>
                </div>
            </el-tab-pane>
            <el-tab-pane label="用例配置" name="second" @click="reLoadTestCase"  >
                <div class="background-type"> 
                <caseConfig></caseConfig>
                <canDialog></canDialog>
                </div>
                <div>

                </div>
            </el-tab-pane>
            <el-tab-pane label="脚本配置" name="third">
                <div>
                    <caseScript></caseScript>
                </div>
            </el-tab-pane>
            <el-tab-pane label="邮件配置" name="thourth">
                <div>
                    <caseEmail></caseEmail>
                </div>
                <div>
                    <caseDialog></caseDialog>
                </div>

            </el-tab-pane>
            <el-tab-pane label="代理地址">
              <proxyAddre></proxyAddre>

            </el-tab-pane>
            <el-tab-pane label="执行日志">
              <caseLog></caseLog>
            </el-tab-pane>

        </el-tabs>
        <div>
          <moveScript></moveScript>
          <addProxy></addProxy>
        </div>
    </div>
</template>
<script>
import uiTree from '../../components/Tree'
import bus from '../../bus'
import qs from 'qs'
import Sortable from 'sortablejs'
import treeUIDialog from '../../components/addAPiDialog'
import caseConfig from '../../components/testcase/caseConfig'
import canDialog from '../../components/testcase/cancelDialog'
import caseScript from '../../components/testcase/caseScript'
import caseStep from '../../components/testcase/caseStepChange'
import caseEmail from '../../components/testcase/caseEmail'
import caseDialog from '../../components/testcase/caseDialog'
import moveScript from '../../components/testcase/moveScript'
import caseLog from '../../components/testcase/testCaseLog'
import proxyAddre from '../../components/testcase/proxyAddress'
import addProxy from '../../components/testcase/addProxyAddress'
export default {
    name:"testcase",
    components:{
        uiTree,
        caseConfig,
        canDialog,
        caseScript,
        caseStep,
        caseEmail,
        caseDialog,
        moveScript,
        treeUIDialog,
        caseLog,
        proxyAddre,
        addProxy
    },
    data(){
        return{
        emailDia:true,
        modelData:'',
        activeName:'first',
        testCaseList:[],
        clearTestcase:true,
        openProxy:true,//控制打开代理地址
        expendRowKey:[],
        freshTest:true,
        moveScript:true,
        reFreshCase:true,
        caseIndex:[],
        keyList:[],
        UITestCases:[],
        expendScript:[],
        methodList:[
            {"value":"0","label":"请选择"},
            {"value":"1","label":"ID"},
            {"value":"2","label":"Name"},
            {"value":"3","label":"CLASS_NAME"},
            {"value":"4","label":"TAG_NAME"},
            {"value":"5","label":"LINK_TEXT"},
            {"value":"6","label":"Xpath"},
            {"value":"7","label":"CSS_SELECTOR"},
             ],
        }
    },
    methods:{
      //点击用例刷新
      reLoadTestCase(){
        bus.$emit('reFreshTestCases',this.reFreshCase)
        console.log("用例刷新")
      },
      addProxyAddress(){
        bus.$emit('openProxy',this.openProxy)
      },
        bulkCopy(){

        },
        moveTestCase(val){
          console.log('移动脚本用例',val)
          let src_moveCaseList={
            srcScriptCase:val,
            srcCaseList:this.testCaseList
          }
          bus.$emit('moveScript',src_moveCaseList)
        },
        copyTestsCase(val){
          console.log('复制用例信息',val)
          this.$axios.post(this.$api.copyTestCase,qs.stringify(val),{emulateJSON: true})
            .then(response=>{
              if(response.data.code==200){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                    this.selectCase(this.modelData)
                }else{
                    this.$message({
                        showClose:true,
                        message:'复制用例失败',
                        type:'fail'
                    })
                }
            })
        },
        copyUiTest(val){
          console.log('保存脚本用例',val)
          this.$axios.post(this.$api.copyScript,qs.stringify(val),{emulateJSON: true})
          .then(response=>{
              if(response.data.code==200){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                    this.selectCase(this.modelData)
                }else{
                    this.$message({
                        showClose:true,
                        message:'复制用例脚本失败',
                        type:'fail'
                    })
                }
            })
        },
        //保存测试脚本排序
        saveTestCaseSort(val){
            console.log('保存排序',val)
            this.$axios.post(this.$api.saveTestCaseSort,qs.stringify(val))
            .then(response=>{
                    if(response.data.code==0){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                    this.selectCase(this.modelData)
                }else{
                    this.$message({
                        showClose:true,
                        message:'排序保存失败', 
                        type:'fail'
                    })
                }
            })
        },
        //行拖拽
        rowDrop() {
            const tbody = document.querySelector(".el-table__body-wrapper tbody");
            const _this = this;
            Sortable.create(tbody, {
                onEnd({ newIndex, oldIndex }) {
                console.log('newIndex',newIndex)
                console.log('oldIndex',oldIndex)
                // const currRow = _this.testCaseList.splice(oldIndex, 1)[0];
                // console.log('currRow',currRow)
                // _this.testCaseList.splice(newIndex, 0, currRow);
                var newDataList=_this.testCaseList[newIndex].testcaseList
                var oldDataList=_this.testCaseList[oldIndex].testcaseList
                console.log('newDataList',oldDataList.map(item=>item.script_id))
                console.log('testCaseList',_this.testCaseList)
                // _this.saveTestCaseSort({
                //     newIndex:_this.testCaseList[newIndex].testcase_id,
                //     oldIndex:_this.testCaseList[oldIndex].testcase_id,
                //     newScriptList:(newDataList.map(item=>item.script_id)),
                //     oldScriptIdList:(oldDataList.map(item=>item.script_id))
                //     });

                  _this.saveTestCaseSort({
                    newIndex:_this.testCaseList[newIndex].testcase_id,
                    oldIndex:_this.testCaseList[oldIndex].testcase_id,
                    // newScriptList:(newDataList.map(item=>item.script_id)),
                    // oldScriptIdList:(oldDataList.map(item=>item.script_id))
                    modelData:_this.modelData,
                    });
                }
            });
            },
        //异步请求
        expandChange(row,rowList){
            console.log(row,rowList)
            
            //获取下标
            for(let i=0;i<  this.testCaseList.length;i++){
                if(this.testCaseList[i]==row){
                    this.caseIndex=i
                    console.log('caseIndex',this.caseIndex)
                }
            }
            if(row.testcase_id==this.expendRowKey){
                this.expendRowKey=[]
            }else{
                this.expendRowKey=[]
                this.expendRowKey.push(row.testcase_id)
            }
            

        },
        //rowClick
        rowClick(row,event,column){
            console.log('点击行')
            console.log(row,event,column)
        },
        //返回row_key
        row_key(val){
            return val.testcase_id
        },
        //保存排序
        saveCaseList(val){
            this.$axios.post(this.$api.saveSortCaseList,qs.stringify(val),{emulateJSON: true})
            .then(response=>{
                    if(response.data.code==0){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                    this.selectCase(this.modelData)
                }else{
                    this.$message({
                        showClose:true,
                        message:'排序保存失败', 
                        type:'fail'
                    })
                }

            })
            
        },
        handleDown(index,row){
            let _this=this
            if(index+1==_this.testCaseList[this.caseIndex].testcaseList.length){
                _this.$message({
                    message: "亲不能再往下移了",
                    type: "info"
                });
            }else{      
                     
            _this.currentRow=row
            const index_id=_this.currentRow.script_id
            _this.newRow=_this.testCaseList[this.caseIndex].testcaseList[index+1]
            _this.currentRow.script_id=_this.newRow.script_id
            _this.newRow.script_id=index_id
            const caseList={
                newID:_this.newRow.script_id,
                currentID:_this.currentRow.script_id
            }
            _this.saveCaseList(caseList)
            this.selectCase(this.modelData)
            this.expendRow(_this.testCaseList[this.caseIndex].testcase_id)
            }
            
        },
        expendRow(val){
            this.expendRowKey=[]
            this.expendRowKey.push(val)
        },
        handleUp(index,row){
            let _this=this
            if(index==0){
                _this.$message({
                    message: "亲不能再往上移了",
                    type: "info"
                });
            }else{
            _this.currentRow=row
            const index_id=_this.currentRow.script_id
            _this.newRow=_this.testCaseList[this.caseIndex].testcaseList[index-1]
            _this.currentRow.script_id=_this.newRow.script_id
            _this.newRow.script_id=index_id
            const caseList={
                newID:_this.newRow.script_id,
                currentID:_this.currentRow.script_id
            }
            _this.saveCaseList(caseList)
            this.selectCase(this.modelData)
            this.expendRow(_this.testCaseList[this.caseIndex].testcase_id)
            }
        },
        //批量执行
        batExecuting(){
            if('0' in this.UITestCases){
                console.log(typeof(this.UITestCases))
                this.$axios.post(this.$api.batchExection,JSON.stringify(this.UITestCases),{emulateJSON: true})
            .then(response=>{
                if(response.data.code==200){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                }else{
                    this.$message({
                        showClose:true,
                        message:'UI用例批量执行失败',
                        type:'fail'
                    })
                }
            })
            }else{
                this.$message({
                        showClose:true,
                        message:'请选择测试用例',
                        type:'info'
                })
            }
        },
        handleChangeExpend(val){

        },
        handleChangeExpand(val){
          console.log('批量复制expend',val)

        },
        //table选项
        handleChange(val){
            console.log('批量用例',val)
            let ScriptList=[]
            for(let i in val){
                let tableList={}
                tableList.testcase_id=val[i].testcase_id
                // tableList.testcaseList=val[i].testcaseList
                ScriptList.push(tableList)
            }
            console.log('ScriptList**********',ScriptList)
            this.UITestCases=ScriptList
            
        },
        //新增Script
        freshScript(){
            bus.$emit('freshScriptCase',this.freshTest)
            this.activeName='third'
        },
        //查询关键字
        selectKey(){
            this.$axios.get(this.$api.selectTestKey)
            .then(response=>{
                console.log(typeof(response.data.keyWord_data[0].value))
                this.keyList=response.data.keyWord_data
            })
        },
        //编辑UI脚本用例
        editUiTest(val){
            console.log("编辑UI用例",val)
            bus.$emit('editUITestCase',val)
            this.activeName='third'
        },
        //删除UI脚本用例
        delUiTest(val){
            this.$axios.post(this.$api.delScriptCase,qs.stringify(val))
            .then(response=>{
                if(response.data.code==200){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                  this.selectCase(this.modelData)
                }else{
                    this.$message({
                        showClose:true,
                        message:'Script用例删除失败',
                        type:'fail'
                    })
                }
            })
        },
        
        //编辑测试用例
        editTestCase(val){
            console.log('编辑主用例',val)
            bus.$emit('editTestCase',val)
            this.activeName='second'
        },
        //删除测试用例
        deleteTestCase(val){
            this.$axios.post(this.$api.delUiTestCase,qs.stringify(val))
            .then(response=>{
                if(response.data.code==200){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                this.selectCase(this.modelData)
                }else{
                    this.$message({
                        showClose:true,
                        message:'UI用例删除失败',
                        type:'fail'
                    })
                }
            })
        },
        //添加邮件配置
        addEmailConfig(){
            this.activeName="thourth"
            bus.$emit('emailDia',this.emailDia)
        },
        //添加接口用例
        addUITestCase(){
            this.activeName="second"
            bus.$emit('clearUITestcase',this.clearTestcase)
        },
        editUITestCase(val){
            this.activeName="second"

        },
        refresh(){
            this.$forceUpdate()
        },
        //查询接口用例
        selectCase(val){
            this.$axios.post(this.$api.selectUITestCase,qs.stringify({modelData:val}))
            .then(response=>{
                if(response.data.code==1000){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'error'
                    })
                }else{
                    this.testCaseList=response.data
                    console.log('获取用例脚本信息',this.testCaseList)
                }
                
            })
        }

    },
    mounted(){
        this.rowDrop()
    },
    created(){
        this.$forceUpdate();
        this.selectKey();//查询关键字
        bus.$on('testCaseList',msg=>{
            console.log('testcse',msg)
            this.modelData=msg
            this.selectCase(msg)
        })
        bus.$on('freshScriptCase',msg=>{
          this.selectCase(this.modelData)
        })
        bus.$on('refreshCaseList',msg=>{
          this.selectCase(this.modelData)
        })
    },
    watch:{
        '$route.path':function(newVal,oldVal){
            if(this.$route.path=='/manage/testcase'){
                this.$forceUpdate()
            }
        },
    }
}
</script>
<style scoped>
.search-style{
        padding-top: 10px;
        /* margin-left: 20px; */
        background-color: #f5f5f5;
        width: 100%;
        height: 10%;
        /* float: left; */
    }
/* .table-type{
    margin-left: 20px;
} */
.background-type{
    /* background-color: #f5f5f5; */
    height: 800px;
    width: auto;
  /*float: left;*/
}
.main-type{
    /* background-color: #f5f5f5; */
    height: 90%;
}
.table-sytle{
    margin-top: 5px;
    margin-left: 20px;
}
.tree-style{
    margin-top: 5px;
    margin-left: 3px;
}
/*.el-dialog__headerbtn{*/
/*  top:5px;*/
/*}*/
.tabs-style{
    margin-top: 5px;
    margin-left: 5px;
}
</style>



