<template>
  <div  class="page-register">
    <div class="page-register--form">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item>
          如果你已经有了一个账号， 请<el-button type="text" @click="returnLogin">登陆</el-button>
        </el-form-item>
  <el-form-item label="用户名">
    <el-input v-model="form.username"></el-input>
  </el-form-item>
  <el-form-item label="密码">
    <el-input v-model="form.password"></el-input>
  </el-form-item>

  <el-form-item>
    <el-button type="primary" @click="onSubmit" class="button-login">立即创建</el-button>
  </el-form-item>
</el-form>
    </div>

  </div>

</template>

<script>
import reportPie from '../../components/report/reportPie'
import airPie from '../../components/report/airPie'
import reprotTable from '../../components/report/reportTable'
import bus from '../../bus'
import qs from 'qs'
export default {
  name: "reportManage",
  components: {
    reportPie,
    airPie,
    reprotTable
  },
  data() {
    return {
      form: {
        username: '',
        password:''
      }
    }
  },
  methods: {
    returnLogin(){
      let redirect = decodeURIComponent(this.$route.query.redirect || '/login');
                    this.$router.push({path: redirect})
    },
    onSubmit() {
      this.$http.post(this.$api.register,{
                'username':this.form.username,
                'password':this.form.password

            },{emulateJSON:true}
            ).then((response)=>{
                if(response.data['code']!=0){
                    this.$message({
                                showClose: true,
                                message: response.data['msg'],
                                type: 'warning',
                            });
                }else{
                    this.$message({
                                showClose: true,
                                message: response.data['msg'],
                                type: 'success',
                            });
                }
            })
    }
  }


}
</script>
<style lang="scss">
    .button-login{
      width: 100%;
    }
    .page-register{
        /*background-color:#F0F2F5;*/
        height: 100%;
        width: 100%;
        position: absolute;
    }
    .page-register--form{
        margin: auto;
        width: 400px;
        margin-top: 10%;
            /* // 登录按钮 */
        .button-login{
            width: 100%;
        }
    }
  .class-zuce{
      float: right;
  }

</style>



