<template>
    <div class="report-type">
        <el-table
        :data="this.caseList"
        height="500"
        :cellStyle="cellStyle"
        @expand-change="handleEx"
        >
          <el-table-column type="expand">
             <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="版本名称">
                  <span>{{ props.row.modeldata_name }}</span>
                </el-form-item>
                <el-form-item label="用例名称">
                  <span>{{ props.row.testcase_caseName }}</span>
                </el-form-item>
                <el-form-item label="脚本名称">
                  <span>{{ props.row.script_testDataName }}</span>
                </el-form-item>
                <el-form-item label="关键字">
                  <span>{{ props.row.script_testDataKeyWord }}</span>
                </el-form-item>
<!--                <el-form-item label="开始执行时间">-->
<!--                  <span>{{ props.row.executing_starttime }}</span>-->
<!--                </el-form-item>-->
                <el-form-item label="结束时间">
                  <span>{{ props.row.executing_endtime }}</span>
                </el-form-item>
                <el-form-item label="参数">
                  <span>{{ props.row.script_testDataParameter }}</span>
                </el-form-item>
                <el-form-item label="定位元素">
                  <span>{{ props.row.script_testDataElement }}</span>
                </el-form-item>
                 <el-form-item label="断言结果">
                  <span>{{ props.row.script_testResult }}</span>
                </el-form-item>
                <el-form-item v-if="props.row.picture_file!=null" label="截图" props="picture_file">
<!--                        <el-image style="width:100%;height:100%"   alt="封面" :src="props.row.picture_file">-->
<!--                        </el-image>-->
<!--                  <template slot-scope="scope">-->
                    <el-image v-if="props.row.script_testResult=='失败'" style="width: 1100px; height: 600px" :src="props.row.picture_file"></el-image>
<!--                  </template>-->


                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
            <el-table-column
            prop="script_id"
            label="ID"
            sortable
            width="70"
            >
            </el-table-column>
            <el-table-column
            prop="testcase_caseName"
            label="用例名称"
            >
            </el-table-column>
            <el-table-column
            prop="script_testDataName"
            label="脚本名称"
            >
            </el-table-column>

            <el-table-column
            prop="script_testDataKeyWord"
            label="关键字"
            >
            </el-table-column>
            <el-table-column
            prop="executing_name"
            label="执行时间"
            >
            </el-table-column>
            <el-table-column
            label="执行结果"
            prop="script_testResult"
            >
                <template slot-scope="scope">
                    <div v-if="scope.row.script_testResult=='成功'">
                        SUCCESS
                        <i class="el-icon-success"></i>
                    </div>
                    <div v-if="scope.row.script_testResult=='失败'">
                        FAIL
                        <i class="el-icon-error"></i>
                    </div>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
import bus from '../../bus'
export default {
    data(){
        return{
            caseList:[],
            case_total:[],
            case_success:[],
            case_fail:[],
            case_null:[],
            pan:''
        }
    },
    methods:{
      handleEx(row){
        // console.log('row',row)
      },
      //清理列表状态
      clearList(){
        this.case_total=[]
            this.case_success=[]
            this.case_fail=[]
            this.case_null=[]
            this.caseList=[]
      },
      cellStyle({ row, column, rowIndex, columnIndex }){
        // console.log('rowww',row)
        if(row.script_testResult=='失败'){
            // bus.$emit('cellTableStyle',row.testcase_caseName)
            return "color:#F56C6C";
        }
      }
    },
    created(){
        bus.$on('caseTable',msg=>{
            this.clearList()
            this.caseList=msg
        });
    }
}
</script>
<style>
.el-icon-success{
    color: green;
}
.el-icon-error{
    color: red;
}
.report-type{
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
}
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



