<template>
    <div id="pie" style="width:350px;height:200px;" class="pie-style">
    </div>
</template>
<style>
.pie-style{
    background-color: #f5f5f5;
    border: 1px;

}
</style>
<script>
import echart from 'echarts'
import bus from '../../bus'
export default {
    data(){
        return{
           testCases:[]
        }
    },
    methods:{
        drawPie(id){
            this.chartPie= echart.init(document.getElementById(id))
            this.chartPie.setOption({
                //  title: {
                //         text: '某站点用户访问来源',
                //         subtext: '纯属虚构',
                //         left: 'center'
                //     },
                backgroundColor: '#dee4ea',
                    tooltip: {},
            // legend: {
            //     data:['成功','失败']
            // },
              legend: {
                        orient: 'vertical',
                        left: 'right',
                        data:['成功','失败'],
                        // bottom: '20%',
                        align:"right",
                        icon: 'circle',
                        itemGap: 20
                    },
              series: [
        {
            name: '测试通过率',
            type: 'pie',
            hoverAnimation: false,
            legendHoverLink: false,
            radius: '70%',
            center: ['50%', '50%'],
            label: {
              show: true,
              formatter: '{d}%'
            },
            data: [
                {   value: this.testCases.fail,
                    name: '失败',
                  // itemStyle:{
                  //       normal:{color:'#22cfe0'},
                  //       emphasis:{color:'#22cfe0'}
                  //   }
                     itemStyle:{
                        normal:{color:'#ffc545'},
                        emphasis:{color:'#ffc545'}
                    }
                },
                {   value: this.testCases.success,
                    name: '成功',
                    // itemStyle:{
                    //     normal:{color:'#ffc545'},
                    //     emphasis:{color:'#ffc545'}
                    // }
                  itemStyle:{
                        normal:{color:'#22cfe0'},
                        emphasis:{color:'#22cfe0'}
                    }
                }
            ]
        },
        {
                name: "大环",
                type: 'gauge',
                splitNumber: 30,
                radius: '80%',
                center: ['50%', '50%'],
                startAngle: 90,
                endAngle: -269.9999,
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: true,
                    length: 5,
                    lineStyle: {
                        color: '#1cbdca'
                    }
                },
                splitLine: {
                    show: false,
                    length: 3,
                },
                axisLabel: {
                    show: false
                },
                detail: {
                    show: false
                }
            },
            {
             type: 'pie',
             zlevel: 2,
             silent: true,
             radius: ['50%', '60%'],
             startAngle: 85,
             label: {
                 normal: {
                     show: false
                 },
             },
             labelLine: {
                 normal: {
                     show: false
                 }
             },
            //  data:[
            //     // {value:this.testCases.total, name:'总计',itemStyle: {color: '#409EFF'}},
            //     {value:null, name:'成功1',itemStyle: {color: '#1cbdca'}},
            //     // {value:this.testCases.count_null, name:'未执行',itemStyle: {color: '#909399'}},
            //     {value:null, name:'失败1',itemStyle: {color: '#E6A23C'}},
            //
            // ],
         },
    ]
               })
        }
    },
    mounted(){
            // this.$nextTick(function() {
            //     this.drawPie('pie')
            // })
        },
    created(){
        console.log(2222)
        bus.$on("reportpie",msg=>{
          console.log('reportpie',msg)
          this.testCases=msg
            // this.testCases=msg.taskData[0]
            this.drawPie('pie')
        })
        
    }
}
</script>



