<template>
<el-row>
    <el-col>
        <el-table
        :data="emailList.slice((pageIndex-1)*pageSize,pageIndex*pageSize)"
        >
            <el-table-column label="所属项目"
            prop="modeldata_name"
            >
            </el-table-column>
            <el-table-column label="发件人"
            prop="email_fromAddr"
            >
            </el-table-column>
            <el-table-column label="收件人"
            prop="email_toAddr"
            >
            </el-table-column>
            <el-table-column label="操 作">
                 <template slot-scope="scope">
                    <el-button size="mini" icon="el-icon-edit" @click="editEmail(scope.row)" type="primary">编 辑</el-button>
                    <el-button size="mini" icon="el-icon-delete" @click="delEmailConfig(scope.row)"  type="danger">删 除</el-button>
                 </template>
                
            </el-table-column>
        </el-table>
      <div class="email-footer">
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageIndex"
                :page-size="pageSize"
                :page-sizes="[10, 20, 50, 100]"
                layout="total, sizes, prev, pager, next, jumper"
                :total="this.emailList.length">
                </el-pagination>
      </div>

    </el-col> 
</el-row>
    
    
</template>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
          emailList:[],
          pageIndex:1,
          pageSize:10,
        }
    },
    methods:{
      handleSizeChange(val){
          this.pageSize=val
        },
        handleCurrentChange(val){
          this.pageIndex=val
        },
    //编辑邮件配置
    editEmail(val){
        // console.log('editEmailData',val)
        bus.$emit('editEmailData',val)
    },
    delEmailConfig(val){
        bus.$emit('delEmailData',val)
    },
    //查询邮件配置
    selectConfig(){
        this.$axios.get(this.$api.selectEConfig)
        .then(response=>{
            this.emailList=response.data
        })
    },
    },
    created(){
        this.selectConfig();
        bus.$on('freshTable',msg=>{
            this.selectConfig()
        })
    }
}
</script>
<style>
  .email-footer{
    text-align: center;
  }

</style>



