<template>
    <div class="page-login">
        <div class="page-login--form">
            <p style="font-size:26px;font-weight:700;font-family:KaiTi">
                测试平台
            </p>
            <el-card shadow="never">
                <el-form ref="loginForm" label-position="top" size="default">
                    <el-form-item>
                        <el-input type="text" v-model="account" placeholder="用户名">
                            <i slot="prepend" class="el-icon-s-custom"></i>
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-input type="password" v-model="password" placeholder="密码">
                            <i slot="prepend" class="el-icon-tickets"></i>
                        </el-input>
                    </el-form-item>
                    <!--<el-button size="default" @click.native="login()" type="primary" class="button-login">-->
                        <!--登录-->
                    <!--</el-button>-->
                  <el-form-item>

<!--                    <el-col :span="7">-->

<!--                    </el-col>-->
<!--                    <el-col :span="17">-->
                        <el-button size="default" @click.native="login()" type="primary" class="button-login">
                        登录
                    </el-button>
<!--                    </el-col>-->
                    没有账号，<el-button size="default" @click.native="registered()" type="text" class="info">
                        去注册
                    </el-button>


                  </el-form-item>

                </el-form>

            </el-card>
        </div>
    </div>
</template>
<script>
import bus from '../bus'
export default {
    data(){
        return {
            token: '',
            account: '',
            password: '',
        }
    },
    methods:{
        //注册
        registered(){
            let redirect = decodeURIComponent(this.$route.query.redirect || '/registered');
            this.$router.push({path: redirect})
        },
        //登录
        login(){
            this.$http.post(this.$api.loginApi,{
                'username':this.account,
                'password':this.password
                
            },{emulateJSON:true}
            ).then((response)=>{
                if(response.data['code']!=0){
                    this.$message({
                                showClose: true,
                                message: response.data['msg'],
                                type: 'warning',
                            });
                }else{
                    this.token=response.data['token']
                    let redirect = decodeURIComponent(this.$route.query.redirect || '/manage');
                    this.$router.push({path: redirect})
                    this.$cookies.set('token',this.token,'1m')
                    bus.$emit('pushUserInfo',response.data.userInfo[0])
                }
            })
        }
    }
}
</script>
<style lang="scss">
    .page-login{
        background-color:#F0F2F5;
        height: 100%;
        width: 100%;
        position: absolute;
    }
    .page-login--form{
        margin: auto;
        width: 280px;
        margin-top: 10%;
            /* // 登录按钮 */
        .button-login{
            width: 97%;
        }
    }
  .class-zuce{
      float: right;
  }

</style>



