<template>
<div>
    <el-dialog
  title="提示"
  :visible.sync="Visible"
  width="30%">
  <span>清除所有已编辑的数据</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="Visible = false" size="mini">取 消</el-button>
    <el-button type="primary" size="mini" @click="clearData">确 定</el-button>
  </span>
</el-dialog>
</div>
</template>
<script>
import bus from '../../bus'
export default {
    data(){
        return{
            Visible:false,
        }
    },
    methods:{
        clearData(){
            bus.$emit('clearData',this.Visible)
            this.Visible=false
        }
    },
    created(){
        bus.$on('cancelDia',msg=>{
            console.log('dialog',msg)
            this.Visible=msg
        })
    }
}
</script>



