<template>
    <div>
        <el-row :gutter="20">
            </el-row>
            <el-row class="reportclass" >
                <el-tabs v-model="activeName"
                >
                   <el-tab-pane
                   label="日志报告" name="first"
                   >
                        <el-table
                        v-loading="executingData"
                        element-loading-background = "rgba(0, 0, 0, 0.5)"
                        element-loading-text="数据正在加载中"
                        :data="tableList.slice((pageIndex-1)*pageSize,pageIndex*pageSize)"
                        >
                            <el-table-column
                            type="index"
                            width="50px"
                            align="center"
                            >

                            </el-table-column>
                           <el-table-column
                            v-if="showAPI"
                            label="类型"
                            prop="executing_testapi_id"
                            align="center"
                            >
                                <template>
                                    <div>
                                    <el-tag>API</el-tag>
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column
                            v-if="showUI"
                            prop="executing_testscript_id"
                            label="类型"
                            align="center"
                            >
                                <template>
                                    <div>
                                    <el-tag>UI</el-tag>
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column
                            label="运行时间"
                            prop="executing_name"
                            >
                            </el-table-column>

                            <el-table-column
                            prop="executing_testmd"
                            label="MD5"
                            width="270"
                            >
                            </el-table-column>
                            <el-table-column
                            label="报告"
                            align="center"
                            >
                            <template slot-scope="scope">
                                <el-tag type="success"
                                @click="TestReport(scope.row)"
                                >
                                    测试报告
                                </el-tag>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="操作"
                            align="center"
                            >
                            <template slot-scope="scope">
                                <el-button @click="deleteExecutingLog(scope.row)" type="danger" size="mini">
                                    删除
                                </el-button>
                            </template>
                            </el-table-column>
                        </el-table>
                        <div class="el-UiExecuting">
                                    <el-pagination
                                        @size-change="handleSizeChange"
                                        @current-change="handleCurrentChange"
                                        :current-page="pageIndex"
                                        :page-size="pageSize"
                                        :page-sizes="[10, 20, 50, 100]"
                                        layout="total, sizes, prev, pager, next, jumper"
                                        :total="this.tableList.length">
                                        </el-pagination>
                                    <!-- </div> -->
                    </div>
                   </el-tab-pane>
                </el-tabs>
            </el-row>
    </div>
</template>
<script>
import qs from 'qs'
export default {
    data(){
        return{
            activeName:'first',
            testApi:'API',
            testUI:'UI',
            tableList:[],
            pageSize:10,
            pageIndex:1,
            showAPI:false,
            showUI:false,
            executingData:true
        }
    },
    methods:{
        //强制刷新日志界面
        handleSizeChange(val){
          this.pageSize=val
        },
        handleCurrentChange(val){
          this.pageIndex=val
        },
        //请求日志报告
        TestReport(val){
            if(this.$route.path=='/manage/executingLog'){
                // this.$router.push({path:'/manage/report',query:{label:val.executing_testmd}})
                let routeData=this.$router.resolve({
                  name:'/manage/report',params:{label:val.executing_testmd}
                })
                window.open(routeData.href,'_bank')
            }else{
                console.log("UI用例")
                // this.$router.push({path:'/manage/UIreport',query:{label:val.executing_testmd}})
              let routeData=this.$router.resolve({
                  path:'/UIreport',query:{label:val.executing_testmd}
                })
                window.open(routeData.href,'_bank')
            }
        },
        //删除日志报告
        deleteExecutingLog(val){
            this.$axios.post(this.$api.deleteExecuting,qs.stringify({executing_testmd:val.executing_testmd}))
            .then(response=>{
                this.selectExecutingLog()
            })
        },
        //查看日志报告
        selectExecutingLog(){
            if(this.$route.path=='/manage/executingLog'){
                // console.log('接口界面')
                this.$axios.post(this.$api.selectExecuting,qs.stringify({testtype:this.testApi}))
                    .then(response=>{
                        this.tableList=response.data
                        this.executingData=false
                        this.showAPI=true
                    })
            }else{
                // console.log('用例界面')
                this.$axios.post(this.$api.selectExecuting,qs.stringify({testtype:this.testUI}))
                    .then(response=>{
                        console.log(response.data)
                        this.tableList=response.data
                        this.executingData=false
                        this.showUI=true
                    })
            }
            
        }
    },
    created(){
        this.selectExecutingLog()
        // this.refreshLog()
    }
}
</script>
<style>
.search-style{
        padding-top: 10px;
        /* margin-left: 20px; */
        background-color: #f5f5f5;
        width: 100%;
        height: 10%;
        /* float: left; */
    }
  .el-UiExecuting{
    text-align: center;
  }
</style>



