<template>
    <div class="projectManage">
        <el-row :gutter="20">
            <el-form :inline="true" size="small" class="demo-form-inline search-style">
                <el-form-item style="margin-left:10px;">
                    <el-button style="margin-left:10px;" size="mini" type="primary" @click="clearModelData()">
                        添加项目
                    </el-button>
                    <el-button size="mini" type="primary" @click="addVariDialog">新增变量</el-button>
                </el-form-item>
            </el-form>
            </el-row>
            <el-tabs>
                <el-tab-pane label="项目">
                    <el-row :gutter="20">
                <div >
                <el-col :span="24">
                    <el-table
                    :data="tableData.slice((pageIndex-1)*pageSize,pageIndex*pageSize)"
                    style="width: 100%">
                    <el-table-column
                        prop="modeldata_id"
                        label="ID"
                        width="100">
                    </el-table-column>
                    <el-table-column
                        prop="modelData"
                        label="项目名称"
                        width="150">
                    </el-table-column>
                    <el-table-column
                        prop="name"
                        label="所属项目"
                        width="150">
                    </el-table-column>
                    <el-table-column
                        prop="modelData_pripeople"
                        label="项目负责人">
                    </el-table-column>
                    <el-table-column
                    align="center"
                    label="操作">
                    <template slot-scope="scope">
                        <el-button type="primary" @click="editModelData(scope.row)" icon="el-icon-edit" size="mini" >编辑</el-button>
                        <el-button type="danger"  @click="deleteModel(scope.row)" icon="el-icon-delete" size="mini">删除</el-button>
                    </template>
                    </el-table-column>
                    </el-table>
                    <div class="el-pagination">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pageIndex"
                        :page-sizes="[10, 20, 50, 100]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="this.tableData.length">
                        </el-pagination>
                    </div>
                </el-col>
                
                </div>
            </el-row>
                </el-tab-pane>
                <el-tab-pane label="全局变量">
                    <globalVar></globalVar>
                </el-tab-pane>
            </el-tabs>
            
            
            <el-dialog
                title="项目配置"
                :visible.sync="addProjectDialog"
                width="45%"
                >
                <el-tabs>
                    <el-tab-pane label="基础信息">
                    <el-form :inline="true">
                        <el-form-item label="项目" label-width="60px" size="mini">
                            <el-input v-model="projectData[0].modelData" style="width:100px">
                            </el-input>
                        </el-form-item>
                        <el-form-item class=".el-form-item__label" label="所属项目" label-width="72px" size="mini">
                            <el-select v-model="projectData[0].subModelData" placeholder="请选择" style="width:100px;">
                                <el-option
                                        v-for="item in modelData"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="负责人" label-width="60px" size="mini">
                            <el-input v-model="projectData[0].modelData_pripeople" style="width:100px">
                            </el-input>
                        </el-form-item>
                    </el-form>
                    <hr>
                    <el-tabs type="card">
                        <el-tab-pane label="测试环境">
                            <el-table :data="projectData" :show-header="false">
                        <el-table-column property="modelData_testenvironment">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.modelData_testenvironment" size="mini"
                                            placeholder="http://127.0.0.1:8010">
                                </el-input>
                            </template>
                        </el-table-column>
                    </el-table>
                        </el-tab-pane>
                        <el-tab-pane label="生产环境">
                            <el-table :data="projectData" :show-header="false">
                                <el-table-column property="modelData_proenvironment">
                                    <template slot-scope="scope">
                                        <el-input v-model="scope.row.modelData_proenvironment" size="mini"
                                                    placeholder="http://127.0.0.1:8010">
                                        </el-input>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-tab-pane>
                    </el-tabs>
                </el-tab-pane>
                    <el-tab-pane label="公用变量">公用变量</el-tab-pane>
                </el-tabs>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addProjectDialog = false" size="small">取 消</el-button>
                    <el-button type="primary" @click="addModelData" size="samll">确 定</el-button>
                </span>
            </el-dialog>
            <variDialog></variDialog>
    </div>
</template>

<script>
import globalVar from '../../components/global/globalVariables'
import variDialog from '../../components/global/variableDialog'
import bus from '../../bus'
import qs from 'qs';
  export default {
    name:"projectManage",
    components:{
        globalVar,
        variDialog
    },
    data() {
      return {
        variableDialog:true,
        projectData:[{
            modeldata_id:'',
            modelData:'',
            subModelData:'',
            modelData_pripeople:'',
            modelData_testenvironment:'',
            modelData_proenvironment:''
        }],
        pageSize:10,
        addProjectDialog:false,
        modelData:[],
        pageIndex: 1,  
        tableData: [],
        data: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };
    },
    methods:{
        
        //新增变量
        addVariDialog(){
            bus.$emit('openVariDialog',this.variableDialog)
        },
        //获取tree信息
        handleNodeClick(data) {
                console.log(data);
            },
        //删除模块信息
        deleteModel(data){
            // console.log("获取到indexID是",data)
            this.$axios.post(this.$api.deleteModelDataList,qs.stringify({modeldata_id:data.modeldata_id}))
            .then(response=>{
                if(response.data.code==200){
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'success',
                    })
                    this.selectAllModelData()
                    this.selectMainModelData()

                }else{
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'fail',
                    })
                }
            })
        },
        //查询模块信息
        selectAllModelTree(){
            this.$axios.get(this.$api.selectAllModelTree)
            .then(response=>{
                this.data=response.data
                // console.log("获取到的所有tree信息为",response.data)
            })
        },
        //编辑模块信息
        editModelData(data){
            // console.log("获取到每行信息",data)
            this.projectData[0]=data
            this.addProjectDialog=true
            // this.addModelData()
        },
        //查询所有模块信息
        selectAllModelData(){
            this.$axios.get(this.$api.selectAllModel)
            .then(response=>{
                this.tableData=response.data
                // console.log(this.tableData)
                // console.log("获取到的所有模块信息是",response.data)
            })
        },
        //查询主模块信息
        selectMainModelData(){
            this.$axios.get(this.$api.selectModelList)
            .then(response=>{
                this.modelData=response.data
                // console.log("获取到的主模块信息是",response.data)
            })
        },
        clearModelData(){//新增前先清空表单
            this.selectMainModelData()
            this.projectData=[{
            modeldata_id:'',
            modelData:'',
            subModelData:'',
            modelData_pripeople:'',
            modelData_testenvironment:'',
            modelData_proenvironment:''
        }]
        this.addProjectDialog=true
        },
        addModelData(){
            console.log("获取到项目信息是",this.projectData)
            if(this.projectData[0].subModelData==''){
                this.projectData[0].subModelData=0
            }
            this.$axios.post(this.$api.saveModelData,qs.stringify(
                this.projectData[0]
            ),
            )
            .then(response=>{
                // console.log(response.data)
                if(response.data.code==200){
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'success',
                    })
                    this.selectAllModelData();
                    this.selectMainModelData();
                    this.addProjectDialog=false;
                }else{
                    this.$message({
                        showClose: false,
                        message: response.data.msg,
                        type: 'fail',
                    });
                }
            })
        },
        handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
    },
    mounted(){
        this.selectAllModelData()
        this.selectMainModelData()
        this.selectAllModelTree()
    }
  };
</script>
<style>
.el-form-item__label{
    /* margin-left: 10px; */
    width: 72px;
}
.el-dialog__body{
    padding-bottom: 5px;
    padding-left: 10px;
    padding-top:5px;
}
.el-dialog__headerbtn{
    top: 6px;
}
.el-dialog__header{
    padding-top:2px;
    padding-right: 10px;
    padding-bottom: 2px;
    padding-left: 10px;
    background-color: #f5f7fa;
    border-radius: 5px;    
}
.el-pagination{
    text-align: center;
}
.el-tree-row{
    border: 1px #dcdfe6;
    background-color:#f5f5f5;
    /* border-color: #dcdfe6; */
}
.el-form-item__label{
    margin-left: 20px;
}
.demo-form-inline{
    /* margin-left: 10px; */
    float: left;
    
}
.search-style{
        padding-top: 10px;
        /* margin-left: 20px; */
        background-color: #f5f5f5;
        width: 100%;
        height: 10%;
        /* float: left; */
    }
</style>



