<template>
  <el-row>
<!--    <el-col :span="1">-->
<!--      <li class="liType" v-for="(i) in logList">{{logList.indexOf(i)}}</li>-->
<!--    </el-col>-->
    <el-col>
        <div id="log-container" style="height: 710px; overflow-y: scroll;color: #FAFBFC; background: #333;padding: 5px;"
          >
            <ul>
              <li class="liType" v-for="(i) in logList">{{i.time}}&nbsp&nbsp{{i.task}}</li>
            </ul>
          </div>
    </el-col>
  </el-row>



</template>
<script>
export default {
    data(){
        return{
          logList:[],
          timer:''
        }
    },
  methods:{
      setTime() {
        this.timer=setInterval(() => {
        this.getTestCaseLog();
          }, 30000)
      },
      getTestCaseLog(){
            this.$axios.get(this.$api.getProLog)
            .then(response=>{
                if(response.data.code=200){
                    this.logList=response.data
                }else{
                    this.$message({
                        showClose: true,
                        message:'请求失败',
                        type: 'error',
                    })
                }

            })
        },

  },
  mounted() {
    this.getTestCaseLog()
  },
  beforeDestroy(){
        clearInterval(this.timer);
      },
  created(){
        this.setTime()
        },
  watch:{
        $route(to,from){
          if(to.path=='/manage/testcase'){
            this.setTime()
          }else{
            window.clearInterval(this.timer);
          }
        }
        },
}
</script>
<style scoped>
  .liType{
    list-style-type:none;
  }
</style>



