<template>
    <div>
        
        <el-row>
            <el-form :inline="true" size="small" class="demo-form-inline search-style">
                    <el-form-item>
                        <el-button style="margin-left:10px;" type="primary" size="mini" @click="addWebTest">
                            添加用例
                        </el-button>
                        <el-button type="primary" size="mini" @click="BatchExectionMethod">
                            批量执行
                        </el-button>
                        <el-button type="primary" size="mini" @click="addCookiesList">
                            新增Cookie
                        </el-button>
                    </el-form-item>
                </el-form>
        </el-row>
        <responseDialog></responseDialog>
    <el-tabs v-model="activeName" style="padding-left:5px;">
        <el-tab-pane label="接口信息" name="first"> 
        <el-row>
            <div>
            <el-col :span="5">
                <el-card class="box-card" shadow="hover">
                <div slot="header" class="clearfix">
<!--                    <el-dropdown trigger="click" @command="handleCommand">-->
<!--                <span class="el-dropdown-link">-->
<!--                    操作<i class="el-icon-arrow-down el-icon&#45;&#45;right"></i>-->
<!--                </span>-->
<!--                <el-dropdown-menu slot="dropdown">-->
<!--                    <el-dropdown-item command="add" icon="el-icon-plus">新增</el-dropdown-item>-->
<!--                    <el-dropdown-item command="edit" icon="el-icon-circle-plus">编辑</el-dropdown-item>-->
<!--                    <el-dropdown-item command="del" icon="el-icon-circle-plus-outline">删除</el-dropdown-item>-->
<!--                </el-dropdown-menu>-->
<!--                </el-dropdown>-->
                
                </div>
                <trees></trees>
                </el-card>
            </el-col>
            <el-col :span="18" style="padding-left:25px;">
                <li class="el-icon-caret-right" style="color: rgb(0, 153, 255);margin-top:10px; "></li>
                <span style="font-size:14px;">接口用例信息</span>
                <!-- <div style="float:right;">
                    <el-button type="info" @click="saveAllTestCase" size="mini"> 保存</el-button>
                </div> -->
                <apiTable></apiTable>
                <passwordFreeDialog></passwordFreeDialog>
                <openDialog v-bind:openlog='openlog'></openDialog>
                <openTestCase v-bind:openTestCase='openTestCase'></openTestCase>
            </el-col>
            </div>
        </el-row>
        </el-tab-pane>
        <el-tab-pane label="接口配置" name="second">
            <apiMsg></apiMsg>
        </el-tab-pane>
        <el-tab-pane label="Cookies配置" name="third">
            <cookiesConfig></cookiesConfig>
            <addCookiesDialog></addCookiesDialog>
        </el-tab-pane>
    </el-tabs>
    </div>
</template>
<style>
.search-style{
        padding-top: 10px;
        /* margin-left: 20px; */
        background-color: #f5f5f5;
        width: 100%;
        height: 10%;
        /* float: left; */
    }
.el-card__body{
    height: 250px;
    padding: 20px;
}
.el-card__header{
    float: right;
    padding: 1px 20px;
}
.el-dropdown-link{
    float: right;
    /* padding: 30px 0; */
}
</style>
<script>
import trees from '../../components/Tree'
import openDialog from '../../components/addAPiDialog'
import openTestCase from '../../components/apidata/addWebTestCase'    
import apiMsg from '../../components/apidata/apiMsg'  
import apiTable from '../../components/apidata/apiTable'  
import responseDialog from '../../components/apidata/responseDialog'
import passwordFree from '../../components/apidata/passwordFreeTable'
import passwordFreeDialog from '../../components/apidata/passwordFreeDialog'
import cookiesConfig from '../../components/apidata/apiCookies'
import addCookiesDialog from '../../components/apidata/addCookies'
import bus from '../../bus'
export default {
    name:"apidata",
    components:{
        trees,
        openDialog,
        openTestCase,
        apiMsg,
        apiTable,
        responseDialog,
        passwordFree,
        passwordFreeDialog,
        cookiesConfig,
        addCookiesDialog
    },
    data(){
        return{
            activeName:"first",
            openlog:false,
            dialogVisible:false,
            freshDialog:false,
            openTestCase:false,
            clearTestCases:true,
            BatchExectionType:true,
            openPassFree:true,
            saveTestCase:true,
            openAddCookies:true,
        }
    },
    methods:{
        //新增cookies
        addCookiesList(){
            this.activeName='third'
            bus.$emit('addCookie',this.openAddCookies)
        },
        //保存全部配置
        saveAllTestCase(){
            bus.$emit('saveAllCases',this.saveTestCase)
        },
        //免密配置
        passFreeDialog(){
            bus.$emit('openPassWordFree',this.openPassFree)
        },
        //批量执行
        BatchExectionMethod(){
            bus.$emit('BatchExection',this.BatchExectionType)
        },
        changeFalse(){ 
            this.openlog=false;
            this.dialogVisible=false
        },
        //新增用例
        addWebTest(){
            this.activeName="second";
            bus.$emit('clearTestCases',this.clearTestCases)
        },
        //新增模块信息
        handleCommand(command){
            if(command=='add'){
                bus.$emit('addVersion',command)
                }
            if(command=='edit'){
                bus.$emit('editVersion',command)
            }
            if(command=='del'){
                bus.$emit('delVersion',command)
            }
        }
    },
    mounted(){
        bus.$on("freshDialog",freshDialog=>{
            this.freshDialog=freshDialog
        })
    },
    created(){
        bus.$on('openTableEdit',msg=>{
            this.activeName=msg
        }
        );
        bus.$on('dialogVisible',msg=>{
            this.dialogVisible=msg
        });
        bus.$on('openlog',msg=>{
            this.openlog=msg
            console.log('监听到的openlog',this.openlog)
        });
        bus.$on('openTestCase',msg=>{
            this.openTestCase=msg
            console.log('监听到的openTestCase',this.openTestCase)
        });
    },
    // watch:{
    //     dialogVisible: function(val,oldVal) {
    //         this.freshDialog=true
    //     }
    // }
}
</script>



