<template>
    <div>
        <el-dialog
            title="新增变量"
            :visible.sync="variableVisible"
            width="30%"
            >
            <el-form :inline="true" :data="variList">
                <el-form-item label="Key">
                    <el-input size="mini" v-model="variList.variKey"></el-input>
                </el-form-item>
                <el-form-item label="Value">
                    <el-input size="mini" v-model="variList.variValue"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="mini" type="info" @click="variableVisible = false">取 消</el-button>
                <el-button size="mini" type="primary" @click="saveGlobalVari">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<style scoped>
.dialog-footer /deep/.dialog-foot{
    text-align: center;
}
</style>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
            variableVisible:false,
            freshDialog:true,
            variList:{
                varId:'',
                variKey:'',
                variValue:''
            }
        }
    },
    methods:{
        //保存全局变量
        saveGlobalVari(){
            this.$axios.post(this.$api.saveVariAble,qs.stringify(this.variList))
            .then(response=>{
                if(response.data.code==200){
                    this.$message({
                    showClose:true,
                    message:response.data.msg,
                    type:'success'
                })
                this.variableVisible=false
                bus.$emit('freshVariDialog',this.freshDialog)
                }else{
                    this.$message({
                    showClose:true,
                    message:'保存变量失败',
                    type:'error'
                })
                }
            })
        }
    },
    created(){
        bus.$on('openVariDialog',msg=>{
            this.variableVisible=true
        })
        bus.$on('editGlobalVariable',msg=>{
            this.variList.varId=msg.variable_id
            this.variList.variKey=msg.variableKey
            this.variList.variValue=msg.variableValue
            this.variableVisible=true
        })
    }
}
</script>



