            </el-aside>
            <el-container>
                <el-header style="height: 30px;">
                    <router-view class="view one" name="Header"></router-view>
                </el-header>
                    <tags v-show="tagsShow"></tags>
                <el-main>
                    <keep-alive :include="tagsList">
                        <router-view class="view two" name="Manage" style="font-family: Arial"
                                     v-if="isRouterAlive"></router-view>
                    </keep-alive>
                </el-main>
            </el-container>
        </el-container>
</template>




<script>
import tags from './Tags.vue';
import bus from '../bus';
  export default {
    name:'manage',
    components:{
        tags
    },
    data() {
      const item = {
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      };
      return {
        isRouterAlive:true,
        tagsList: [],
        tagsShow:true,
        collapsed:false,
        navigationName: '/manage/projectManage',
        tableData: Array(20).fill(item)
      }
    },
    methods:{
        collapse: function () {
                this.collapsed = !this.collapsed;
            },
        closeNavigation(){
            this.navigationName = this.$route.path;
        }
    },
    watch:{
        "$route":function(to){
            this.navigationName = to.path;
        }
    },
    mounted() {
            this.closeNavigation()
        },
    created(){
        bus.$on('tags',msg=>{
            let arr=[];
            for(let i=0,len=msg.length;i<len;i++){
                arr.push(msg[i].name)
            }
            this.tagsList=arr;
            // console.log('manage获取到的taglist是',this.tagsList,'');
        })
    }
  };
</script>
<style>
body{
    margin: 0px;
}
    .element.style{
        margin:0px;
    }
    .container-body{
        margin: 0%;
    }
    .el-main{
        text-align: left;
        padding: 0%;
    }
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 220px;

    }
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  .menu-toggle {
        background: #434650;
        text-align: center;
        color: white;
        height: 30px;
        line-height: 30px;
    }
  .el-aside {
        background-color: #545c64;
        color: #333;
        text-align: left;
        line-height: 200px;
    }
</style>



