<template>
    <el-dialog
    :visible.sync="openResponseDialog"
    title="接口返回值"
    width="50%"
    >
            <el-collapse accordion>
        <!-- <el-collapse-item>
            <template slot="title">
            status_code
            </template>
            <div></div>
        </el-collapse-item> -->
        <el-collapse-item :title="name">
            <div>{{content}}</div>
        </el-collapse-item>
        </el-collapse>
    </el-dialog>
    
</template>
<script>
import bus from '../../bus'
export default {
    data(){
        return{
            name:'',
            status_code:'',
            text:'',
            encoding:'',
            content:'',
            openResponseDialog:false
        }
    },
    created(){
        bus.$on('openReDialog',msg=>{
            console.log('执行接口用例')
            this.openResponseDialog=!this.openResponseDialog
            this.name=msg.name
            this.content=msg.responseData
        })
    }
}
</script>


