<template>
    <div>
        <el-table :data="variableList" >
            <el-table-column label="key" prop="variableKey">
            </el-table-column>
            <el-table-column label="value" prop="variableValue">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="editVariable(scope.row)">编辑</el-button>
                    <el-button size="mini" type="danger" @click="delVariable(scope.row)">删除</el-button>
                </template>
                
            </el-table-column>
            
        </el-table>
    </div>
</template>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
            variableList:[],

        }
    },
    methods:{
        //查找全局变量
        selectVariable(){
            this.$axios.get(this.$api.selectGlobalVariable)
            .then(response=>{
                console.log(response.data)
                this.variableList=response.data.msg
            })
        },
        //编辑全局变量
        editVariable(val){
            console.log('编辑',val)
            bus.$emit('editGlobalVariable',val)
        },
        //删除全局变量
        delVariable(val){
            console.log('删除',val)
            this.$axios.post(this.$api.delVariable,qs.stringify(val))
            .then(response=>{
                if(response.data.code==200){
                    this.$message({
                    showClose:true,
                    message:response.data.msg,
                    type:'success'
                })
                this.selectVariable()
                }else{
                    this.$message({
                    showClose:true,
                    message:'删除变量失败',
                    type:'error'
                })
                }
            })
        }
    },
    created(){
        this.selectVariable()
        bus.$on('freshVariDialog',msg=>{
            this.selectVariable()
        })
    }
}
</script>


