import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import manage from '@/components/Manage'
import Header from '@/components/Header'
import projectManage from '@/prijectView/project/project.vue'
import reportManage from '@/prijectView/report/report.vue'
import executingLog from '@/prijectView/executingLog/executing.vue'
import apiData from '../prijectView/api/apidata.vue'
import testcase from '../prijectView/UI/testcase.vue'
import UIReport from '../prijectView/UIReport/UIReport'
import UIExecuting from '../prijectView/UIExecutingLog/UIExecuting'
import projectLog from '../prijectView/log/log'
import keyWord from '../components/testcase/keyWord'
import login from '../login/login.vue'
import vueRsource from 'vue-resource'
import  Registered from '../prijectView/Registered/registered'
import $cookie from 'vue-cookie'

Vue.use(Router)
// Vue.use(VueRouter)
Vue.use(vueRsource)

const routes=[
  {
    path: '/manage',
    // name: 'Manage',
    component: manage,
    children:[
      {
        path:'',
        components:{
          Header:Header,
          Manage:projectManage,
        },
        meta: {
          title: '项目管理',
      }
      },
      
    {
      path: 'projectManage',
      meta: {
          title: '项目管理',
          requireAuth: true,
          keepAlive: true
      },
      components: {
          Header: Header,
          Manage: projectManage,
      },
  },
  {
    path: 'apidata',
    meta: {
        title: '接口信息',
        requireAuth: true,
        keepAlive: true
    },
    components: {
        Header: Header,
        Manage: apiData,
    },
},
{
  path: 'report',
  meta: {
      title: '测试报告',
      requireAuth: true,
      keepAlive: true
  },
  components: {
      Header: Header,
      Manage: reportManage,
  },
},
{
path: 'executingLog',
meta: {
    title: '执行日志',
    requireAuth: true,
    keepAlive: true
},
components: {
    Header: Header,
    Manage: executingLog,
},
},
{
path: 'testcase',
meta: {
    title: '测试用例',
    requireAuth: true,
    keepAlive: true
},
components: {
    Header: Header,
    Manage: testcase,
},
},
// {
// path: 'UIReport',
// meta: {
//     title: 'UI测试报告',
//     requireAuth: true,
//     keepAlive: true
// },
// components: {
//     Header: Header,
//     Manage: UIReport,
// },
// },
{
path: 'UIExecuting',
meta: {
    title: 'UI执行日志',
    requireAuth: true,
    keepAlive: true
},
components: {
    Header: Header,
    Manage: UIExecuting,
},
},
{
  path: 'projectLog',
  meta: {
      title: '系统日志',
      requireAuth: true,
      keepAlive: true
  },
  components: {
      Header: Header,
      Manage: projectLog,
  },
  },
  {
    path: 'keyWord',
    meta: {
        title: '关键字信息',
        requireAuth: true,
        keepAlive: true
    },
    components: {
        Header: Header,
        Manage: keyWord,
    },
    },

    ]
  },
  {
      path: '/login',
      // component:caseManage,
      component: login,
  },
  {
      path: '',
      // component:caseManage,
      component: login,
  },
  {
      path: '/registered',
      component: Registered,
  },
  {
path: '/UIReport',
meta: {
    title: 'UI测试报告',
    // requireAuth: true,
    keepAlive: true
},
    component:UIReport,
// components: {
//     // Header: Header,
//     Manage: UIReport,
// },
},
]
// export default new Router({
// // const router =new Router({  
//   // mode: 'history',  
  
// })
const router = new Router({
  // mode: 'history', //去掉路径中的#
  routes
});
router.beforeEach((to, from, next) => {
  if (to.matched.some(r => r.meta.requireAuth)) {
        // fetch('/login').then(response=>{
        //   if(response.data==200){
        //     next();
        //   }else{
        //     if(getCookie('token')){
        //       console.log(222)
        //     }
        //   }
        // })
      if ($cookies.get('token')) {
          next();
          console.log('login_1')
      }
      else {
          console.log('login_2')
          console.log(to.fullPath)
          next({
              path: '/',
          })
      }
  }
  else {
      next();
  }
});
export default router;
