<template>
<el-dialog
  title="移动用例信息"
  :visible.sync="movedialogVisible"
  width="30%">
  <el-table
  :data="movetestCaseList"
  stripe
  @selection-change="handleSelectionChange"
  >
    <el-table-column
    type="selection"
    >
    </el-table-column>
    <el-table-column
    prop="testcase_caseName"
    label="用例信息"
    >

    </el-table-column>
  </el-table>
  <span slot="footer" class="dialog-footer">
    <el-button @click="movedialogVisible = false" size="mini">取 消</el-button>
    <el-button type="primary" @click="saveBatchCaseID" size="mini">确 定</el-button>
  </span>
</el-dialog>
</template>

<script>
  import bus from '../../bus'
import qs from 'qs'
    export default {
        data(){
          return{
            movetestCaseList:[],
            choseCase:[],
            movedialogVisible:false,
            srcScriptList:{},
            caseIDList:[],
            freshScriptCase:true
          }
        },
      methods:{
      saveBatchCaseID(){
        if(this.caseIDList.length<2){
           this.$axios.post(this.$api.copyScript,qs.stringify(this.srcScriptList))
             .then(response=>{
               if(response.data.code==200){
                 this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                 this.movedialogVisible=false
                 bus.$emit('freshScriptCase',this.freshScriptCase)
               }else{
                 this.$message({
                        showClose:true,
                        message:'复制脚本用例出错',
                        type:'error'
                    })
               }
             })
        }else{
          this.$message({
                        showClose:true,
                        message:'请选择单条测试用例',
                        type:'info'
                    })
        }
      },
      //获取需要转移的caseID
      handleSelectionChange(val){
        console.log('val',val)
        // this.caseIDList=[]
        // for(let i in val){
        //   this.caseIDList.push(val[i]['testcase_id'])
        // }
        this.srcScriptList['script_TestDataCase']=val[0]['testcase_id']
        console.log(this.srcScriptList)
        }
      },
      created(){
          bus.$on('moveScript',msg=>{
            this.movedialogVisible=true
            this.movetestCaseList=msg.srcCaseList
            this.srcScriptList=msg.srcScriptCase
            console.log('moveScript',msg)
          })
      }
    }
</script>

<style scoped>

</style>



