<template>
    <div class="apimsg">
        <el-form :inline="true">
            <el-form-item label="项目信息">
                <el-select size="mini" placeholder="请选择项目信息" v-model="apiMsg.modelData">
                    <el-option
                        v-for="item in dialogVersion"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-select size="mini" placeholder="请选择版本信息" v-model="apiMsg.versionData">
                    <el-option
                        v-for="item in versionList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-select size="mini" placeholder="请选择Cookies" v-model="apiMsg.addCookiesValue">
                    <el-option
                        v-for="item in cookiesList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="用例名称">
                <el-input placeholder="请输入用例名称" size="mini" v-model="apiMsg.apiName">
                </el-input>
            </el-form-item>
            <el-form-item>
                <el-input placeholder="请输入Host地址" style="width:1100px;margin-top:5px;" v-model="apiMsg.apiHost">
                    <el-select v-model="apiMsg.apiRequest" slot="prepend" placeholder="协议" style="width:100px;">
                        <el-option label="http" value="http"></el-option>
                        <el-option label="https" value="https"></el-option>
                     </el-select>
                </el-input>
            </el-form-item>
            <!-- <el-form-item>
                <el-switch
                v-model="apiMsg.cookiesValue"
                inactive-text="添加cookies"
                >
                </el-switch>
            </el-form-item> -->
            <el-form-item>
                <el-switch
                v-model="apiMsg.addPassWordFree"
                inactive-text="提取cookies"
                >
                </el-switch>
            </el-form-item>
            <el-form-item>
                <el-switch
                v-model="apiMsg.passWordFree"
                inactive-text="URL提取"
                >
                </el-switch>
            </el-form-item>
        </el-form>
        <hr style="height:1px;border:none;border-top:1px solid rgb(241, 215, 215);">
        <el-form style="margin-top:5px;">
            <el-form-item>
                <el-input placeholder="请输入内容"  class="input-with-select" v-model="apiMsg.apiUrl"
                style="width:80%;"
                >
                    <el-select v-model="apiMsg.apiMethod" slot="prepend" style="width:100px;" placeholder="请选择"
                    >
                    <el-option label="GET" value="1"></el-option>
                    <el-option label="POST" value="2"></el-option>
                    <el-option label="PUT" value="3"></el-option>
                    <el-option label="DELETE" value="4"></el-option>
                    </el-select>
                    <!-- <el-button slot="append"> 变量</el-button> -->
                </el-input>
                <el-button type="primary" size="small" @click="apiRequestData" >Request</el-button>
                <el-button type="primary" size="small" @click="saveTestCaseList">Save</el-button>
            </el-form-item>
        </el-form>
        <el-tabs
        v-model="activeName"
        >
            <el-tab-pane label="Body" name="first">
                <div style="border:1px solid rgb(234, 234, 234)">
                        <el-input  type="textarea" rows="25"
                        style="width:100%;height:550px;"
                        v-model="apiMsg.apiBody"
                         ></el-input>
                </div>
            </el-tab-pane>
            <el-tab-pane label="Extract" name="second">
                <div style="height:300px;padding:20px;">
                         <el-form :inline="true" size="mini">
                             <el-form-item label="变量名">
                                 <el-input v-model="apiMsg.apiExtractName"></el-input>
                             </el-form-item>
                             <el-form-item label="表达式">
                                 <el-input v-model="apiMsg.apiExtractExpression"  style="width:350px;"></el-input>
                             </el-form-item>
                             <el-form-item label="提取值">
                                 <el-input v-model="apiMsg.apiExtractResponse" type="textarea" rows="9" style="width:640px;height:400px;"></el-input>
                             </el-form-item>
                         </el-form>
                </div>
            </el-tab-pane>
            <el-tab-pane label="Assert" name="third">
                <div style="border:1px solid rgb(234, 234, 234)">
                        <el-input  type="textarea" rows="25"
                        style="width:100%;height:550px;"
                        v-model="apiMsg.apiAssert"
                         ></el-input>
                </div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>

import {quillEditor } from 'vue-quill-editor'
import bus from '../../bus'
import qs from 'qs'
// import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
// import 'quill/dist/quill.bubble.css'
export default {
    components:{
        quillEditor 
    },
    data(){
        return{
            mianmi:'',
            content: '',
            activeName:'first',
            editorOption: {},
            dialogVersion:[],
            cookiesList:[],
            versionList:[],
            apiMsg:{
                pId:null,
                cookiesValue:false,
                addPassWordFree:false,
                passWordFree:false,
                modelData:null,
                versionData:null,
                apiName:null,
                apiHost:null,
                apiRequest:null,
                apiMethod:null,
                apiUrl:null,
                apiBody:``,
                addCookiesValue:'',
                apiExtractName:'',
                apiExtractExpression:'',
                apiExtractResponse:'',
                apiAssert:null,
                apiResponse:null
            }
        }
    },
    methods:{
        //初始化全局变量
        InitializeGlobalVariables(){
            this.$axios.get(this.$api.selectGlobalVariable)
            .then(response=>{
                for(var i in response.data.msg){
                    window[response.data.msg[i].variableKey]=response.data.msg[i].variableValue
                }
            })
        },
        //保存正则表达式
        saveExectra(){
            console.log('exectra',this.apiMsg.apiExtract)
        },
        //保存接口用例
        saveTestCaseList(){
            this.saveExectra()
            this.$axios.post(this.$api.saveApiTestCaseList,qs.stringify(this.apiMsg))
            .then(response=>{
                if(response.data.code==0){
                    this.$message({
                        showClose:false,
                        message:response.data.msg,
                        type:'success'
                    })
                }else{
                    this.$message({
                        showClose:false,
                        message:'fail',
                        type:'error'
                    })  
                }
            })
        },
        //接口请求
        apiRequestData(){
            let initializationApiMsg=this.stringEscape(this.apiMsg)
            this.$axios.post(this.$api.apiRequest,qs.stringify(initializationApiMsg))
            .then(response=>{
                console.log('接口信息',response.data)
                if(response.data.code==200){
                    bus.$emit('openReDialog',{name:response.data.apiName,responseData:response.data.data})
                }else{
                    this.$message({
                        showClose:true,
                        message:'fail',
                        type:'error'
                    })
                }
            })
        },
        selectCookiesData(){
            this.$axios.get(this.$api.selectCookiesSelection)
            .then(res=>{
                console.log('cookiesList',res.data)
                this.cookiesList=res.data
            })
        },
        selectData(){
            this.$axios.post(this.$api.selectModelVersion)
            .then(response=>{
                this.dialogVersion=response.data
            })
        },
        selectVerList(val){
            this.$axios.post(this.$api.selectVersionList,qs.stringify({modelData:val}))
            .then(response=>{
                this.versionList=response.data
            })
        }
    },
    created(){
        this.selectCookiesData();
        this.InitializeGlobalVariables();//设置全局变量
        this.selectData();
        bus.$on('clearTestCases',msg=>{
            console.log('清空接口配置',msg)
            if(msg){
                this.apiMsg={
                pId:null,
                cookiesValue:false,
                addPassWordFree:false,
                passWordFree:false,
                modelData:null,
                versionData:null,
                apiRequest:null,
                apiName:null,
                apiHost:null,
                apiMethod:null,
                apiUrl:null,
                apiBody:'',
                addCookiesValue:'',
                apiExtractName:'',
                apiExtractExpression:'',
                apiExtractResponse:'',
                apiAssert:null,
                apiResponse:null
            }
            }
        });
        bus.$on('editTableData',msg=>{
            console.log('msg',msg)
            this.apiMsg.pId=msg.testapi_id;
            this.apiMsg.cookiesValue=msg.testcookiesValue==='false'?false:true;
            this.apiMsg.addPassWordFree=msg.testaddPassWordFree==='false'?false:true;
            this.apiMsg.passWordFree=msg.testpassWordFree==='false'?false:true;
            this.apiMsg.apiAssert=msg.testapiAssert
            this.apiMsg.apiResponse=msg.testapiResponse
            this.apiMsg.modelData=msg.testmodelData
            this.apiMsg.apiRequest=msg.testapiRequest
            this.apiMsg.versionData=msg.Modelversion_id_id
            this.apiMsg.apiName=msg.testapiname
            this.apiMsg.apiHost=msg.testapiHost
            this.apiMsg.addCookiesValue=msg.testaddCookiesValue
            this.apiMsg.apiExtractName=msg.testapiExtractName
            this.apiMsg.apiExtractExpression=msg.testapiExtractExpression
            this.apiMsg.apiExtractResponse=msg.testapiExtractResponse
            this.apiMsg.apiMethod=String(msg.testapiMethod)
            this.apiMsg.apiUrl=msg.testapiUrl
            this.apiMsg.apiBody=msg.testapiBody
            this.apiMsg.apiExtract=msg.testapiExtract
        })
    },
    watch:{
        "apiMsg.modelData":{
            handler(newVal,oldVla){
                this.selectVerList(newVal)
            },
            deep:true
        }

    }
}
</script>
<style>
.apimsg{
    background-color: #f5f5f5;
}
.el-textarea__inner{
    width:100%;
}
</style>


