<template>
    <div>
        <el-row>
            <el-col :span="15">
                <el-card class="card-type-case">    
                    <el-scrollbar style="height:725px;width:100%;">
                    <div class="div-content-case">
                    <el-form :inline="true"  :model="testCaseList"  ref="testCaseList" >
                      <h3 class="kb_editques-title">
                          <i class="el-icon-info">新增测试用例</i>

                        </h3>
                      <hr style="width: 700px;float: left;">
                    <el-form-item  prop="caseName" >
                        <div class="kb_editques-stem-title question_blocks">
                              <span>用例名称</span>
                            </div>
                        <el-input class="input_class"  v-model="testCaseList.caseName" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <!-- <el-divider></el-divider> -->
                      <div class="kb_editques-stem-title question_blocks">
                              <span>前置条件</span>
                            </div>
                        <quillEditor v-model="testCaseList.casePrecondition" ref="myQuillEditor" style="height:200px;width:700px;" :options="editorOption">
                        </quillEditor>
                    </el-form-item>
                    <el-form-item class="editor-item">
                      <div class="kb_editques-stem-title question_blocks">
                              <span>操作步骤</span>
                            </div>
                        <quillEditor v-model="testCaseList.caseSteps" ref="myQuillEditor" style="height:250px;width:700px;" :options="editorOptionTwo">
                        </quillEditor>
                    </el-form-item>
                    <el-form-item class="editor-item">
                      <div class="kb_editques-stem-title question_blocks">
                              <span>预期结果</span>
                            </div>
                        <quillEditor v-model="testCaseList.caseExpected" ref="myQuillEditor" style="height:300px;width:700px;" :options="editorOptionThree">
                        </quillEditor>
                    </el-form-item>
                </el-form>
                </div>
                </el-scrollbar>
                </el-card>
            </el-col>
            <el-col :span="8" style="float:right;" class="card-type-title">
                <el-card >
                    <el-form :inline="true">
                        <el-form-item label="版本信息">
                            <el-select size="mini" v-model="testCaseList.caseVersion" placeholder="请选择">
                                <el-option
                                v-for="item in optionsVersion"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="用例类型">
                            <el-select size="mini" v-model="testCaseList.caseType" placeholder="请选择">
                                <el-option
                                v-for="item in optionsKinds"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="用例等级">
                            <el-select size="mini" v-model="testCaseList.caseLeve" placeholder="请选择">
                                <el-option
                                v-for="item in optionsGrade"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-divider></el-divider>
                        <el-form-item class="congfig-button">
                            <el-button size="mini" @click="saveTestCase" type="primary">
                                提交
                            </el-button>
                            <el-button size="mini" @click="cancelDialog" type="info">
                                重置
                            </el-button>
                        </el-form-item>
                    </el-form>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import {quillEditor} from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import bus from '../../bus'
import qs from 'qs'
export default {
    name:'caseConfig',
    components:{
        quillEditor
    },
    data(){
        return{
            content: null,
            contentTwo:null,
            contentThree:null,
            cancellog:true,
            value:'',
            refreshTestCase:true,
            optionsVersion:[],
            testCaseList:{
                id:'',
                caseName:'',
                casePrecondition:'',
                caseVersion:'',
                caseSteps:'',
                caseExpected:'',
                caseType:'',
                caseLeve:''
            },
            optionsGrade:[{
                value:"1",
                label:"主流程"
            },
            {
                value:"2",
                label:"例行回归"
            },
            {
                value:"3",
                label:"生产回归"
            },
            {
                value:"4",
                label:"普通"
            },
            ],
            optionsKinds:[{
                value:"1",
                label:"需求测试"
            },
            {
                value:"2",
                label:"回归测试"
            },
            {
                value:"3",
                label:"业务自动化"
            },
            ],
            editorOption: {
                placeholder: "请输入前置条件",
                theme: "snow", // or 'bubble' 
                modules: {
                    toolbar:[
                        ['bold', 'italic', 'underline', 'strike'],    //加粗，斜体，下划线，删除线
                        ['blockquote', 'code-block'],     //引用，代码块
                        [{ 'header': 1 }, { 'header': 2 }],        // 标题，键值对的形式；1、2表示字体大小
                        [{ 'list': 'ordered'}, { 'list': 'bullet' }],     //列表
                        [{ 'script': 'sub'}, { 'script': 'super' }],   // 上下标
                        [{ 'indent': '-1'}, { 'indent': '+1' }],     // 缩进
                        [{ 'direction': 'rtl' }], 
                    ]                    
                    }
                },
            editorOptionTwo: {
                placeholder: "请输入操作步骤",
                theme: "snow", // or 'bubble' 
                modules: {
                    toolbar:[
                        ['bold', 'italic', 'underline', 'strike'],    //加粗，斜体，下划线，删除线
                        ['blockquote', 'code-block'],     //引用，代码块
                        [{ 'header': 1 }, { 'header': 2 }],        // 标题，键值对的形式；1、2表示字体大小
                        [{ 'list': 'ordered'}, { 'list': 'bullet' }],     //列表
                        [{ 'script': 'sub'}, { 'script': 'super' }],   // 上下标
                        [{ 'indent': '-1'}, { 'indent': '+1' }],     // 缩进
                        [{ 'direction': 'rtl' }], 
                    ]                    
                    }
                },
            editorOptionThree: {
                placeholder: "请输入预期结果",
                theme: "snow", // or 'bubble' 
                modules: {
                    toolbar:[
                        ['bold', 'italic', 'underline', 'strike'],    //加粗，斜体，下划线，删除线
                        ['blockquote', 'code-block'],     //引用，代码块
                        [{ 'header': 1 }, { 'header': 2 }],        // 标题，键值对的形式；1、2表示字体大小
                        [{ 'list': 'ordered'}, { 'list': 'bullet' }],     //列表
                        [{ 'script': 'sub'}, { 'script': 'super' }],   // 上下标
                        [{ 'indent': '-1'}, { 'indent': '+1' }],     // 缩进
                        [{ 'direction': 'rtl' }], 
                    ]                    
                    }
                },

            }
    },
    methods:{
        //取消清空测试用例
        cancelDialog(){
            bus.$emit('cancelDia',this.cancellog)
        },
        //清空测试用例
        clearTestCase(){
            this.testCaseList={
                id:'',
                caseName:'',
                casePrecondition:'',
                caseVersion:'',
                caseSteps:'',
                caseExpected:'',
                caseType:'',
                caseLeve:''
            }
        },
        saveTestCase(){
            console.log('测试用例',this.testCaseList)
            if(this.testCaseList.caseName==""){
                return this.$message({
                        showClose:true,
                        message:'用例名称不能为空',
                        type:'info'
                    })
            }
            if(this.testCaseList.caseVersion==""){
                return this.$message({
                        showClose:true,
                        message:'版本信息不能为空',
                        type:'info'
                    })
            }
            if(this.testCaseList.caseType==""){
                return this.$message({
                        showClose:true,
                        message:'用例类型不能为空',
                        type:'info'
                    })
            }
            if(this.testCaseList.caseLeve==""){
                return this.$message({
                        showClose:true,
                        message:'用例等级不能为空',
                        type:'info'
                    })
            }
            this.$axios.post(this.$api.saveTestCase,qs.stringify(this.testCaseList))
            .then(response=>{
                console.log('保存测试用例',response.data)
                if(response.data.code==200){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                    bus.$emit('refreshTestCase',this.refreshTestCase)
                }else{
                    this.$message({
                        showClose:true,
                        message:'用例保存失败',
                        type:'fail'
                    })
                }
            })
        },
        selectVersion(){
            this.$axios.get(this.$api.selectVersionData)
            .then(response=>{
                this.optionsVersion=response.data
            })
        }
    },
    created(){
        this.selectVersion();
        bus.$on("clearUITestcase",msg=>{
            // console.log('清除',msg)
            this.clearTestCase()
        });
        bus.$on("reFreshTestCases",msg=>{
          this.selectVersion()
        })
        bus.$on('editTestCase',msg=>{
            this.testCaseList.id=msg.testcase_id
            this.testCaseList.caseName=msg.testcase_caseName
            this.testCaseList.casePrecondition=msg.testcase_casePrecondition
            this.testCaseList.caseVersion=Number(msg.testcase_caseVersion)
            this.testCaseList.caseSteps=msg.testcase_caseSteps
            this.testCaseList.caseExpected=msg.testcase_caseExpected
            this.testCaseList.caseType=msg.testcase_caseType
            this.testCaseList.caseLeve=msg.testcase_caseLeve
        })
        bus.$on('clearData',msg=>{
            this.testCaseList={
                id:'',
                caseName:'',
                casePrecondition:'',
                caseVersion:'',
                caseSteps:'',
                caseExpected:'',
                caseType:'',
                caseLeve:''
            }
        })
    }
}
</script>
<style scoped>

.input_class{
	width: 700px;
}
.el-form--inline .el-form-item{
    margin-right: 0px;
}
.congfig-button{
    float: right;
}
.caseConfig{
    height: 300px;
}
.el-form>>>.el-form-item{
  margin-bottom: 3px;
}
.div-content-case{
    height:500px;
    width:725px;
    border:1px;
}
.el-scrollbar__wrap {
   overflow-x: hidden;
}
.card-type-case{
    padding: 0px;
    padding-left: 5px;
    /* height: 1200px; */
    height: 750px;
    /*width: 775px;*/
    /* @el-card__body{
        height: 500px;
    } */
}
.el-card >>> .el-card__body{
    height: 250px;
}
.card-type-title/deep/.el-card__body{
    padding: 0px;
    width: 325px;
}
.editor-item{
    margin-top:60px;
    margin-bottom: 50px;
}
.kb_editques-stem-title{
    clear: both;
    background-color: #e5e5e5;
    padding: 0 3px;
    line-height: 40px;
    margin-top: 15px;
    margin-bottom: 3px;
    position: relative;
    width:auto;
    margin-top: 5px;
}
/* .el-form--inline .el-form-item{
    margin-top:10px;
    margin-bottom: 40px;
} */
.ql-toolbar.ql-snow{
    padding: 0px;
}
</style>



