<template>
    <div>
        <el-dialog
        title="新增用例"
        :visible.sync="dialogVisible"
        width="50%"
        >
        <el-form :inline="true" :label-position="labelPosition">
            <el-form-item label="用例名称">
            <el-input placeholder="请输入内容" size="mini"></el-input>
            </el-form-item>
            <el-form-item  label="所属项目">
                <el-select size="mini">
                    <el-option
                        v-for="item in versionData"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="域名">
            <el-input placeholder="请输入内容" size="mini">
            </el-input>
            </el-form-item>
            <el-form-item label="请求方法">
            <el-select size="mini">
                    <el-option
                        v-for="item in requestMethods"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="请求URL">
            <el-input size="mini" placeholder="请输入内容"></el-input>
            </el-form-item>
            <el-form-item label="断言">
            <el-input size="mini" placeholder="请输入内容"></el-input>
            </el-form-item>
            <!-- <el-form-item  label="wiki地址">
            <el-input size="mini" placeholder="请输入内容" style="width:510px;">
                <template slot="prepend">Http://</template>
            </el-input>
            </el-form-item> -->
            <el-form-item label="请求参数">
            <el-input size="mini" placeholder="请输入内容" type="textarea" rows="4"></el-input>
            </el-form-item>
            <el-form-item  label="预期结果">
            <el-input size="mini" placeholder="请输入内容" type="textarea"  rows="4"></el-input>
            </el-form-item>
             
            <!-- <el-form-item label="执行结果">
            <el-input placeholder="请输入内容"></el-input>
            </el-form-item> -->
        </el-form>
        <!-- <hr style="border:none;"> -->
        <span slot="footer">
            <el-button type="primary" size="mini">取消</el-button>
            <el-button type="primary" size="mini">保存</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<style>
.el-textarea__inner{
    width:510px;
}
</style>
<script>
import {getVersionData} from '../../js/apidata/addWebTestCase.js'
export default {
    props:{
        openTestCase:{
            type:Boolean,
            default:false
        }
    },
    data(){
        return{
            labelPosition:"right",
            dialogVisible:false,
            versionData:[],
            requestMethods:[
                {"value":"1","label":"GET"},
                {"value":"2","label":"POST"},
                {"value":"3","label":"HEAD"},
                {"value":"4","label":"OPTIONS"},
                {"value":"5","label":"PUT"},
                {"value":"6","label":"DELETE"},
                {"value":"7","label":"TRACE"},
                {"value":"8","label":"CONNECT"},
            ],
            TestCaseData:{

            }
        }
    },
    methods:{
        selectVersionData(){
        this.$axios.get(this.$api.selectVersionData)
            .then(response=>{
                this.versionData=response.data
        console.log(this.versionData)
    })
        }
    },
    created(){
        this.selectVersionData()
    },
    watch:{
        openTestCase:function(val,oldVal){
            this.dialogVisible=true;
        }
    }
}
</script>


