<template>
    <div class="content-top">
                <!-- <el-row> -->
                <div style="text-align:right;">
                <el-button type="info" size="mini" @click="getLog">刷新</el-button>
                    </div>
                 <el-divider></el-divider>
                <div class="content-middle">
                    <el-timeline
                    v-for="(item,index) in (itemList.slice((pageIndex-1)*pageSize,pageIndex*pageSize))" :key="index"
                    >
                        <el-timeline-item :timestamp="item.time" placement="top">
                            <div class="project-type">
                            <p>{{item.task}}</p>
                            </div>
                        </el-timeline-item>
                    </el-timeline>
                </div>
                 <el-footer>
                    <div class="el-pagination">
                        <el-pagination
                            @size-change="handleSizeChange" 
                            @current-change="handleCurrentChange" 
                            :current-page.sync="pageIndex"
                            :page-sizes="[10, 20, 50, 100]"
                            :page-size="pageSize"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="this.itemList.length">
                        </el-pagination>
                    </div>
                </el-footer>
                
    </div>
</template>
<script>
export default {
    data(){
        return{
            itemList:[],
            pageIndex:1,
            pageSize:10
        }
    },
    methods:{
        // 每页显示的条数
       handleSizeChange(val) {
           console.log(val)
           // 改变每页显示的条数 
           this.pageSize=val
           // 注意：在改变每页显示的条数时，要将页码显示到第一页
           this.pageIndex=1
       },
         // 显示第几页
       handleCurrentChange(val) {
           // 改变默认的页数
           this.pageIndex=val
       },
        getLog(){
            this.$axios.get(this.$api.getProLog)
            .then(response=>{
                if(response.data.code=200){
                    this.$message({
                        showClose: true,
                        message:'刷新成功',
                        type: 'success',
                    })
                    this.itemList=response.data
                }else{
                    this.$message({
                        showClose: true,
                        message:'请求失败',
                        type: 'error',
                    })
                }
                
            })
        }
    },
    created(){
        this.getLog()
    }
}
</script>
<style scoped>
.content-middle{
    height: auto;
}
.el-container.is-vertical>>>.el-main{
    background-color: black;
}
.content-top{
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1mm;
    border-radius: 2px;
    padding: 20px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    border-color: bisque;
    height: auto;
}
.project-type{
    border: 1mm;
    border-radius: 2px;
    padding: 20px;
    background-color: #DCDFE6;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    border-color: bisque;
}
.block{
    padding: 20px;
    height: 100%;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)
}
</style>


