<template>
<div>
    <el-table
    :data="this.addressList.slice((pageIndex-1)*pageSize,pageIndex*pageSize)"
    >
      <el-table-column label="版本" prop="modeldata_name">
      </el-table-column>
      <el-table-column label="代理地址" prop="pro_address">
      </el-table-column>
      <el-table-column label="是否开启巡检" prop="pro_openXunjian">
         <template slot-scope="scope">
                    <div v-if="scope.row.pro_openXunjian=='true'">
                        已开启
                        <i class="el-icon-success" style="color:lightgreen;"></i>
                    </div>
                    <div v-if="scope.row.pro_openXunjian=='false' || scope.row.pro_openXunjian==''">
                        已关闭
                        <i class="el-icon-error"></i>
                    </div>
                </template>
      </el-table-column>
      <el-table-column label="操作">
         <template slot-scope="scope">
           <el-button @click="deleteAddre(scope.row)" type="danger" size="mini">删除</el-button>
           <el-button @click="openAddress(scope.row)" size="mini"  type="primary">编辑</el-button>
         </template>
      </el-table-column>
    </el-table>
      <div class="el-proAdress">
                                    <el-pagination
                                        @size-change="handleSizeChange"
                                        @current-change="handleCurrentChange"
                                        :current-page="pageIndex"
                                        :page-size="pageSize"
                                        :page-sizes="[10, 20, 50, 100]"
                                        layout="total, sizes, prev, pager, next, jumper"
                                        :total="this.addressList.length">
                                        </el-pagination>
                                    <!-- </div> -->
                    </div>
</div>
</template>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
            Visible:false,
            openAddre:true,
            addressList:[],
            pageIndex:1,
            pageSize:10,
        }
    },
    methods:{
      handleSizeChange(val){
          this.pageSize=val
        },
        handleCurrentChange(val){
          this.pageIndex=val
        },
      //编辑代理地址
      openAddress(val){
        bus.$emit('openAdd',val)
      },
      //删除代理地址
      deleteAddre(val){
        console.log('val=======111===',val)
        this.$axios.post(this.$api.deleteAdd,qs.stringify(val))
          .then(response=>{
            if(response.data.code==200){
                        this.$message({
                            showClose:true,
                            message:response.data.msg,
                            type:"success"
                        })
                        // bus.$emit('freshTable',this.freshTable)
                        this.selectAddress()
                    }else{
                        this.$message({
                            showClose:true,
                            message:"Fail",
                            type:"fail"
                        })
                    }
          })
      },
      //查找代理地址
      selectAddress(){
          this.$axios.get(this.$api.selectAddress)
            .then(response=>{
              this.addressList=response.data.msg
            })
      }
        // clearData(){
        //     bus.$emit('clearData',this.Visible)
        //     this.Visible=false
        // }
    },created(){
      this.selectAddress()
        bus.$on('freshAddress',msg=>{
          this.selectAddress()
            // console.log('dialog',msg)
            // this.Visible=msg
        })
    }
    //
}
</script>
<style>
  .el-proAdress{
    text-align: center;
  }
</style>



