<template xmlns:el-col="http://www.w3.org/1999/html">
    <div class="reportType">
        <el-row>
            <el-col :span="12">
                <div class="report-header">
                    <span class="font-style"> UI自动化测试报告</span>
                    <span class="font-style-date"> Report Date {{this.reportDate}}</span>
                    <!-- <div class="font-date">
                    <span >Report Date</span>
                    </div> -->
                </div>
            </el-col>
            <el-col :span="12">
                <div class="report-header-date">
<!--                  <el-button type="text" >发送邮件</el-button>-->
<!--                  <el-button type="text">下载PDF</el-button>-->

                </div>
            </el-col>
            <hr>
<!--            <el-col :span="24"><i class="el-icon-s-data"></i></el-col>-->
            <el-col :span="6" >
              <div style="margin-left: 20px;margin-top: 20px;text-align: left;">
                  <el-card class="card-content">
                      <div>
                        <span class="content-font">项目名称：{{this.modelVersion}}</span>
                      </div>
                      <div>
<!--                        <span class="content-font">构建编号：</span>-->
                      </div>
                      <div>
                        <span class="content-font">运行结果：{{this.modelResult}}</span>
                      </div>
                      <div>
                        <span class="content-font">总耗时：{{this.modelTotalTime}}分钟</span>
                      </div>
                  </el-card>

              </div>

            </el-col>
          <el-col :span="17">
            <div style="float:right;">
                <reportPie></reportPie>
              </div>
              <div style="float: right;">
                <airPie></airPie>
              </div>
            </el-col>
            <el-col :span="24">
              <div style="text-align: left">
                <span> Summary</span>
              </div>

              <hr>
                        <el-table  label="用例名称" :data="testRate" style="border:0;text-align:center;width:97%;height:50px;margin-left:20px;margin-right:5px;" :header-cell-style="{background:'#2674a6',height:'5px',color:'#ffffff'}">
                            <el-table-column label=" Totals" prop="total">
                            </el-table-column>
                            <el-table-column label=" Success" prop="success">
                            </el-table-column>
                            <el-table-column label="Failures" prop="fail">
                            </el-table-column>
                            <el-table-column label="Success Rate" prop="successRate">
                            </el-table-column>
                            <el-table-column label="Failures Rate" prop="failRate">
                            </el-table-column>
<!--                            <el-table-column label="Average Time">-->
<!--                            </el-table-column>-->
<!--                            <el-table-column label="Min Time">-->
<!--                            </el-table-column>-->
<!--                            <el-table-column label="Max Time">-->
<!--                            </el-table-column>-->
                        </el-table>
              <hr>

                </el-col>
<!--            <el-col :span="6">-->
<!--                <airPie></airPie>-->
<!--            </el-col>-->
<!--            -->
<!--            <el-col :span="10" class="el-col-type">-->
<!--                        <reportPie></reportPie>-->
<!--                </el-col>-->
            <el-col :span="4">
                  <!--<el-card class="box-card">-->
                  <div style="margin-left: 10px;">
                    <el-table height="500" :cell-style="cellStyle" :data="data" @row-click="rowClick">
                      <el-table-column prop="caseName">
                      </el-table-column>
                    </el-table>
                  </div>

                  <!--</el-card>-->
            </el-col>
            <el-col :span="19">
                    <div style="margin-left: 40px;">
                      <div style="margin-left:5px;text-align: left;">
                          <el-button type="primary" @click="getTotao" size="mini">总计({{this.totalNum}})</el-button>
                          <el-button type="success" @click="getSuccess" size="mini">成功({{this.successNum}})</el-button>
                          <el-button type="warning" @click="getFail" size="mini">失败({{this.failNum}})</el-button>
                      </div>

                      <reprotTable></reprotTable>
                    </div>
                </el-col>

        </el-row>
    </div>
</template>
<script>
import reportPie from '../../components/report/reportPie'
import airPie from '../../components/report/airPie'
import reprotTable from '../../components/report/reportTable'
import qs from  'qs'
import bus from '../../bus'
export default {
    components:{
        reportPie,
        airPie,
        reprotTable
    },
    data(){
        return{
          data: [],
          testRate:[],
          modelVersion:'',
          modelResult:'',
          modelTotalTime:'',
          reportDate:'',
          totalNum:'',
          successNum:'',
          failNum:'',
          src_caseList:[],
          success_list:[],
          fail_list:[],
          case_list:[],
          cell_style:[]
        }
    },
    methods:{
      refreshLog(){
        this.$router.go(0)
      },
        //格式过滤
        cellStyle({ row, column, rowIndex, columnIndex }){
          if(row.result=='失败'){
            return "color:#F56C6C";
          }
        },
        //过滤单条用例信息
        rowClick(row, event, column){
            console.log(row.caseName)
          this.case_list=[]
          for(let simple in this.src_caseList){
            if(this.src_caseList[simple]['testcase_caseName']==row.caseName){
              this.case_list.push(this.src_caseList[simple])
            }
          }
          bus.$emit('caseTable',this.case_list)
        },
        //获取全部用例信息
        gettotal(){
          // this.data=this.src_caseList
          this.totalNum=this.src_caseList.length//总长度
          for(let case_simple in this.src_caseList){
            console.log('case_simple',this.src_caseList[case_simple])
            if(this.src_caseList[case_simple]['script_testResult']=='成功'){
                this.success_list.push(this.src_caseList[case_simple])
            }else if(this.src_caseList[case_simple]['script_testResult']=='失败'){
                this.fail_list.push(this.src_caseList[case_simple])
            }else{
              continue
            }
          }
          console.log('success_list',this.success_list)
          this.successNum=this.success_list.length //成功列表长度
          this.failNum=this.fail_list.length //失败列表长度
        },
        //获取所有信息
        getTotao(){
            // this.data=this.src_caseList
            bus.$emit('caseTable',this.src_caseList)
        },
        //获取成功信息
        getSuccess(){
          // this.data=this.success_list
          bus.$emit('caseTable',this.success_list)
        },
        //获取失败信息
        getFail(){
          // this.data=this.fail_list
          bus.$emit('caseTable',this.fail_list)
        },
        //获取用例信息
        getTestCases(val){
            this.$axios.post(this.$api.selectTestReportDetail,qs.stringify(val))
              .then(response=>{
                console.log('response',response)
                this.data=response.data.msg.testCase_list
                this.src_caseList=response.data.msg.testScrpit_list
                bus.$emit('caseTable',response.data.msg.testScrpit_list)//脚本用例
                bus.$emit('airPieData',response.data.msg.summary)//仪表盘
                bus.$emit("reportpie",response.data.msg.summary)//饼状图
                this.testRate.push(response.data.msg.summary)
                this.modelVersion=response.data.msg.modelVersion
                this.modelResult=response.data.msg.testResult
                this.modelTotalTime=response.data.msg.totalTime
                this.reportDate=response.data.msg.reportDate
                this.gettotal()

              })
            // this.refreshLog()
        }
    },
    mounted(){
      // this.refreshLog()
    },
    created(){
        this.getTestCases({executing_testmd:this.$route.query.label})
        // this.refreshLog()
        bus.$on('cellTableStyle',msg=>{
          this.cell_style.push(msg)
        })
    },
    watch:{
        '$route.query':function(){
        if('value' in this.$route.query){
            this.gettotal()
            }
        }
    }
}
</script>
<style scoped>
  .content-font{
    margin-top: 0px;
    margin-bottom: 0px;
  }
.text-content{
    flex:1;
    font-size:14px;
    line-height:20px;
}
.el-table>>> td, .el-table >>>th{
  padding: 2px 0;
}
.reportType{
    background-color: #dee4ea;
}
 element.style {
    top:30px;
  }
.report-header-date{
    height: 25px;
    width: 100%;
    padding-top: 10px;
    margin-left: 2px;
    margin-right: 10px;
    text-align: right;
}
.report-header{
    /* background-color: #000000; */
    height: 25px;
    width: 100%;
    text-align: left;
    padding-top: 5px;
    margin-left: 2px;
    margin-right: 5px;
}
.el-table__header tr,
  .el-table__header th {
    padding: 0;
    height: 10px;
}
.font-date{
    text-align: right;
    font-size:12px;
    margin-left: 20px;
    margin-bottom: 20px;
}
.font-style-date{
    color: #000000;
    margin-top:10px;
    font-size: 2px;
    text-align: right;
    font-weight: 400;
}
.font-style{
    color: #000000;
    margin-top:10px;
    font-size: 20px;
    font-weight: 700;
}
.card-content{
  background-color: #EBEEF5;
  height: 100px;
}
.item {
  margin-top: 10px;
  margin-right: 20px;
}
  .box-card{
    margin-top:0px;
    width:100%;
    height: auto;
    margin-left: 10px;
  }
</style>



