<template>
    <div>
        <!-- <el-row :gutter="20">
        <el-col :span="4" class="el-tree-row" style="border:1px #dcdfe6;margin-left:2px;margin-top:3px;"> -->
                <div slot="header" class="clearfix">
                    <el-dropdown trigger="click" @command="handleCommand">
                <span class="el-dropdown-link">
                    操作<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="add" icon="el-icon-plus">新增</el-dropdown-item>
                    <el-dropdown-item command="edit" icon="el-icon-circle-plus">编辑</el-dropdown-item>
                    <el-dropdown-item command="del" icon="el-icon-circle-plus-outline">删除</el-dropdown-item>
                </el-dropdown-menu>
                </el-dropdown>
                </div>
      <el-tree
                ref="tree"
                node-key="id"
                :default-expanded-keys="[2,3]"
                :default-checked-keys="[3]"
                :data="datalist"
                @node-click="handleNodeClick"
                :props="defaultProps">
                </el-tree> 
    </div>
</template>
<style>
  .clearfix{
    text-align: right;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
</style>
<script>
import bus from '../bus'
export default {
    // props:{
    //     freshDialog:{
    //         type:Boolean,
    //         default:false,
    //     }
    // },
    data(){
        return{
            datalist:[],
            defaultProps: {
                children: 'children',
                label: 'label'
            },
            timer:null,
            openlog:false,
            freshDialog:null,
            // tableData:null
        }
    },
    methods:{
      //新增模块信息
        handleCommand(command){
            if(command=='add'){
                bus.$emit('addVersion',command)
                }
            if(command=='edit'){
                bus.$emit('editVersion',command)
            }
            if(command=='del'){
                bus.$emit('delVersion',command)
            }
        },
        //判断路由
        choiceRoute(){

        },
        selectData(){
            this.$axios.post(this.$api.selectVersionData)
            .then(response=>{
                // console.log('获取version信息',response.data)
                // this.dialogVersion=response.data
                bus.$emit('tableData',response.data[0].label)
            })
        },
        selectAllModelTree(){
            this.$axios.get(this.$api.selectAllModelTree)
            .then(response=>{
                this.datalist=response.data
                console.log(response.data)
                this.freshDialog=null
            })
        },
        //查询接口用例
        handleNodeClick(data){
            let tableData=data.label
            console.log('route',this.$route)
            console.log("获取到的tableData",tableData)
            if(this.$route.path=="/manage/testcase"){
                console.log('用例界面')
                bus.$emit('testCaseList',tableData)
            }else{
                bus.$emit('tableData',tableData);
            }   
        }
    },
    created(){
        this.selectData();
        this.selectAllModelTree();
        bus.$on('freshDialog',msg=>{
            this.freshDialog=msg
            console.log('监听到的freshDialog',this.freshDialog)
        });
    },
    
    watch:{
        freshDialog: function(val,oldVal){
            this.selectAllModelTree()
            console.log('刷新了freshDialog',this.freshDialog)
        },
    }
}
</script>



