<template>
    <div>
        <el-table :data="cookiseList">
            <el-table-column label="Name" prop="cookie_name">
            </el-table-column>
            <el-table-column label="value" prop="cookie_value">
            </el-table-column>
            <el-table-column label="Domain" prop="cookie_domain">
            </el-table-column>
            <el-table-column label="Path" prop="cookie_path">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="editCookies(scope.row)" >编辑</el-button>
                    <el-button size="mini" type="danger" @click="delCookies(scope.row)">删除</el-button>
                </template>
                
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
            cookiseList:[]
        }
    },
    methods:{
        //编辑cookies
        editCookies(val){
            bus.$emit('editCookiesList',this.cookiseList)
        },
        //删除cookies
        delCookies(val){
            console.log('删除ID',val)
            this.$axios.post(this.$api.delCookie,qs.stringify({cookie_id:val.cookie_id}))
            .then(res=>{
                if(res.data.code==200){
                    this.$message({
                        showClose:true,
                        message:res.data.msg,
                        type:'success'
                    })
                }else{
                    this.$message({
                        showClose:true,
                        message:'删除失败',
                        type:'error'
                    })
                }
                this.selectCookies()
            })
        },
        //查找cookies
        selectCookies(){
            this.$axios.get(this.$api.selectCookie)
            .then(res=>{
                this.cookiseList=res.data
            })
        }
    },
    created(){
        this.selectCookies();
        bus.$on('freshCookiesDialog',msg=>{
            this.selectCookies()
        })
    }
}
</script>


