<template>
    <el-dialog
  title="上传文件"
  :visible.sync="upladDialogVisible"
  width="30%"
>
    <el-upload
    class="upload-demo"
    drag
    action="/quality/saveUpLoad/"
    :data="projectList"
    multiple>
    <i class="el-icon-upload"></i>
    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
<!--    <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>-->
  </el-upload>
  <span slot="footer" class="dialog-footer">
    <el-button size="mini" @click="upladDialogVisible = false">取 消</el-button>
    <el-button size="mini" type="primary" @click="upladDialogVisible = false">确 定</el-button>
  </span>
</el-dialog>
</template>

<script>
import qs from 'qs'
import bus from '../../bus'
    export default {
        name: "uploadfile",
        data(){
          return{
            upladDialogVisible:false,
            projectList:{
              "projectName":"kbp"
            }
          }
        },
      methods:{

      },
      created() {
        bus.$on('openUploadFile',msg=>{
          this.upladDialogVisible=true
        })
      }
    }
</script>

<style scoped>

</style>



