<template>
    <div class="table-type">
    <el-table 
    :data="testCasesList"
    :row-key="this.testCasesList.testapi_id"
    @selection-change="handleSelectionChange"
    @sort-change="handlerChange"
    border
    >
        <el-table-column
        type="selection"
        width="55">
        </el-table-column>
        <el-table-column
        align="center"
        :sortable="'custom'"
        prop="testapi_id"
        width="50px"
        label="ID">
        </el-table-column>
        <el-table-column
        prop="testapiHost"
        label="HOST">
        </el-table-column>
        <el-table-column
        prop="testapiname"
        label="接口名称">
        </el-table-column>
        <el-table-column
        label="URL地址"
        prop="testapiUrl"
        >
        </el-table-column>
        <el-table-column
        width="200px"
        label="操作"
        >
        <template slot-scope="scope">
            <el-button type="primary" @click="editTestCases(scope.row)" icon="el-icon-edit" size="mini" >编辑</el-button>
            <el-button type="danger"  @click="deleteTestCases(scope.row)" icon="el-icon-delete" size="mini">删除</el-button>
        </template>
        </el-table-column>
    </el-table>
    <div class="el-pagination">
                    <el-pagination
                        :current-page="pageIndex"
                        :page-sizes="[10, 20, 50, 100]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="this.testCasesList.length">
                        </el-pagination>
                    </div>
    </div>
</template>
<script>
import bus from '../../bus'
import Sortable from 'sortablejs'
import qs from 'qs'
export default {
    data(){
        return{
            tableData:'',
            testCasesList:[],
            testCaseID:[],
            pageSize:10,
            pageIndex:1,
            activeName:'second',
            batchList:[]
        }
    },
    methods:{
        handlerChange(row, column, cell, event){
            console.log('row',row)
        },
        //保存排序
        saveTestCaseSort(val){
            this.$axios.post(this.$api.saveTestCaseSortList,JSON.stringify(val,{ indices: false }))
            .then(response=>{
                if(response.data.code==200){
                        this.$message({
                            showClose:true,
                            message:response.data.msg,
                            type:'success'
                        })
                        this.selectApiTestCases(this.tableData)
                    }else{
                        this.$message({
                            showClose:true,
                            message:"error",
                            type:'error'
                        })
                    }
            })
        },
        //行拖拽
        rowDrop(){
                const tbody=document.querySelector('.el-table__body-wrapper tbody')
                const _this=this
                Sortable.create(tbody,{
                onEnd({ newIndex, oldIndex }){
                  const currRow = _this.testCasesList.splice(oldIndex,1)[0]
                  _this.testCasesList.splice(newIndex, 0, currRow)
                  var resetList=[]
                  for(var i in _this.testCasesList){
                      resetList.push(_this.testCasesList[i].testapi_id)
                  }
                  _this.saveTestCaseSort({resetList,testCaseID:_this.testCaseID})
                }
                })
        },
        //选中测试用例
        handleSelectionChange(val){
            this.batchList=val
            console.log('选中接口列表',this.batchList)
        },
        //批量执行接口用例
        todoBatch(val){
            if('0' in val){
            this.$axios.post(this.$api.todoBatchExection,JSON.stringify(val))
                .then(response=>{
                    console.log('批量执行接口脚本',response.data)
                    if(response.data.code==200){
                        this.$message({
                            showClose:true,
                            message:"用例批量执行成功",
                            type:'success'
                        })
                    }
                })
            }else{
                this.$message({
                    showClose:true,
                    message:"请勾选接口用例",
                    type:'info'
                })
            }
            
        },
        //搜索接口用例
        selectApiTestCases(val){
            this.$axios.post(this.$api.selectApiCases,qs.stringify({tableData:val}))
            .then(response=>{
                this.testCasesList=response.data
                for(let i in response.data){
                    this.testCaseID.push(response.data[i].testapi_id)
                }
            })
        },
        editTestCases(val){
            bus.$emit('openTableEdit',this.activeName)
            bus.$emit('editTableData',val)
        },
        deleteTestCases(val){
            this.$axios.post(this.$api.deleteApiTestCases,qs.stringify({testapi_id:val.testapi_id}))
            .then(response=>{
                if(response.data.code==0){
                    this.$message({
                        showClose: true,
                        message: response.data.msg,
                        type: 'success',
                    })
                this.selectApiTestCases(this.tableData)
                }else{
                    this.$message({
                        showClose: true,
                        message:"删除失败",
                        type: 'fail',
                    })
                }
            })
        }
    },
    mounted(){
        this.rowDrop()
        // this.sortTestCase()
    },
    created(){
        // bus.$on('saveAllCases',msg=>{
        //     const _this=this
        //     for(var i=0;i<_this.testCaseID.length;i++){
        //         console.log(_this.testCasesList[i])
        //         _this.testCasesList[i]["testapi_id"]=_this.testCaseID[i]
        //     }
        //     console.log('用例排序',_this.testCasesList)
        // })
        bus.$on('tableData',msg=>{
            this.tableData=msg
            console.log('监听到的tableData',this.tableData)
            this.selectApiTestCases(this.tableData)
            
        });
        bus.$on('BatchExection',msg=>{
            console.log('监听到了tableData批量执行',msg)
            console.log('batchList',this.batchList)
            this.todoBatch(this.batchList)
        })
    },
    watch:{
        'testCasesList':function(newVal,oldVal){
            console.log('用例列表变了')
        }
    }
}
</script>
<style>
.el-pagination{
    float: right;
}
.table-type{
    margin-top: 20px;
}
</style>


