<template>
    <div id="airPie"  class="pie-style">
    </div>
    
</template>
<style>
  /*element.style {*/
  /*  top:30px;*/
  /*}*/

.pie-style{
    /*background-color: #ffffff;*/
    width:300px;
    height:200px;
    border: 1px;

}
</style>
<script>
import echart from 'echarts'
import bus from '../../bus'
export default {
    data(){
        return{
           pieTime:'',
           pieId:'',
           passRate:[],
           precentRate:null,
        }
    },
    methods:{
        drawPie(id){
            let data = [{
                name: "达标率",
                value: this.precentRate
            }];
            this.chartAirPie= echart.init(document.getElementById(id))
            this.chartAirPie.setOption(
              {
                backgroundColor: '#dee4ea',
                "title": [
                            {
                            "text": this.pieTime+"通过率",
                            "right": 50,
                            "top": 10,
                            "textStyle": {
                                "color": "#b3b3b3",
                                "fontSize": 12,
                                "fontWeight": "normal"
                            }
                            },
                            {
                            // "text": "任务ID："+this.pieId,
                            "bottom": "20px",
                            "left": "center",
                            "textStyle": {
                                "color": "#b3b3b3",
                                "fontSize": 12,
                                "fontWeight": "normal"
                            }
                            }
                        ],
                "grid": {
                          // "height": 160,
                          // "left": 0,
                          "top": '20%',
                          // "right": 0,
                          // "bottom": 0,
                          // "width":300
                      },
                "series": [{
                      name: "Indicator",
                    center: ["50%", "70%"],
                      type: "gauge",
                      radius: "110%",
                      endAngle: 0,
                      startAngle: 180,
                      min: 0,
                      max: 100,
                      axisLine: {
                          show: true,
                          lineStyle: {
                              width: 20,
                              color: [
                                  // 			颜色渐变函数 前四个参数分别表示四个位置依次为 右上左下
                                  [this.precentRate/100, new echart.graphic.LinearGradient(0, 0, 1, 0, [{
                                      offset: 0,
                                      color: 'rgba(0, 214, 240, 0.4)' // 0% 处的颜色
                                  }, {
                                      offset: 1,
                                      color: 'rgba(0, 214, 240, 1)' // 100% 处的颜色
                                  }], false)],
                                  [1, new echart.graphic.LinearGradient(0, 1, 0, 0, [{
                                      offset: 0.5,
                                      color: '#ccc' // 0% 处的颜色
                                  }, {
                                      offset: 0,
                                      color: '#ccc' // 100% 处的颜色
                                  }], false)],
                              ],
                          },
                      },
                      //仪表盘轴线
                      axisLabel: {
                          show: true,
                          color: "#ffffff",
                          fontSize: 10,
                      },
                      title: {
                          show: false,
                          fontSize: 20,
                          color: "#fff",
                          offsetCenter: [-48, 40]
                      },
                      detail: {
                          offsetCenter: [0, 40],
                          fontSize:15,
                          color:'#fff',
                          formatter: function(value) {
                              return '通过率\r\r{value|' + value + '}\r%';
                          },
                          rich: {
                              value: {
                                  fontSize: 20,
                                  color: "rgba(0, 214, 240, 1)",
                              }
                          }
                      },
                      data: data
                  }]

                // "series": [
                //             {
                //             "name": "仪表盘",
                //             "type": "gauge",
                //             "axisLine": {
                //                 "lineStyle": {
                //                 "color": [
                //                     [
                //                     this.precentRate,
                //                     "#5DD1FA"
                //                     ],
                //                     [
                //                     1,
                //                     "#f7f9fc"
                //                     ]
                //                 ],
                //                 "width": 10
                //                 }
                //             },
                //             "axisLabel": {
                //                 "show": false
                //             },
                //             "axisTick": {
                //                 "show": false
                //             },
                //             "splitLine": {
                //                 "show": false
                //             },
                //             "itemStyle": {
                //                 "show": false
                //             },
                //             "title": {
                //                 "color": "#7f7f7f",
                //                 "fontSize": 16,
                //                 "fontWeight": "bolder"
                //             },
                //             "detail": {
                //                 "offsetCenter": [
                //                 0,
                //                 0
                //                 ],
                //                 "textStyle": {
                //                 "fontSize": "48",
                //                 "fontWeight": "bolder",
                //                 "color": "#000"
                //                 }
                //             },
                //             "pointer": {
                //                 "show": false
                //             },
                //             "data": [this.passRate
                //             ]
                //             },
                //             {
                //             "title": {
                //                 "color": "#7f7f7f",
                //                 "fontSize": 13,
                //                 "offsetCenter": [
                //                 0,
                //                 "40%"
                //                 ]
                //             },
                //             "type": "gauge",
                //             "splitNumber": 0,
                //             "startAngle": 0,
                //             "endAngle": 0,
                //             "splitLine": {
                //                 "show": false
                //             },
                //             "axisLabel": {
                //                 "show": false
                //             },
                //             "pointer": {
                //                 "show": false
                //             },
                //             "detail": {
                //                 "show": false
                //             },
                //             "data": [
                //                 {
                //                 "name": "测试通过率"
                //                 }
                //             ]
                //             }
                //         ]
               }
               )

        }
    },
    mounted(){
            this.$nextTick(function() {
                this.drawPie('airPie')
            })
        },
    created(){
        bus.$on('airPieData',msg=>{
          console.log('airPieData',msg)
            this.precentRate=((msg.success/msg.total)*100).toFixed(2)
            this.drawPie('airPie');
        }
        )
        
    }
}
</script>



