<template>
    <div class="reportType">
        <el-row>
            <el-col :span="24">
                <div class="report-header">
                    <span class="font-style"> 接口自动化测试报告</span>
                    <div class="font-style-time">
                        {{this.reportTitle}}
                    </div>
                </div>
            </el-col>
            <el-col :span="6" class="el-col-type">
                <airPie></airPie>
                </el-col>
                <el-col :span="6" style="margin-top:200px;">
                    <div class="text-content">
                    <div>
                        测试负责人：
                    </div>
                    <div>
                        开始任务时间：{{this.taskDetail.taskStartTime}}
                    </div>
                    <div>
                        结束任务时间：{{this.taskDetail.taskEndTime}}
                    </div>
                    <div>
                        测试通过率：{{this.taskDetail.passRate}}%
                    </div>
                    </div>
                </el-col>
                <el-col :span="10" class="el-col-type">
                        <reportPie></reportPie>
                </el-col>
                <el-col :span="24">
                    <div>
                    <el-button type="primary" size="mini" @click="getExecutingSuccess('总计')">总计({{this.CaseData.total}})</el-button>
                    <el-button type="success" size="mini" @click="getExecutingSuccess('执行成功')">执行成功({{this.CaseData.count_success}})</el-button>
                    <el-button type="info" size="mini" @click="getExecutingSuccess('未执行')">未执行({{this.CaseData.count_null}})</el-button>
                    <el-button type="warning" size="mini" @click="getExecutingSuccess('执行失败')">执行失败({{this.CaseData.count_fail}})</el-button>
                    </div>
                </el-col>
                <el-col :span="24">
                    <reprotTable></reprotTable>
                </el-col>
                <el-col :span="6" style="padding-top:10px;padding-left:10px;">
                    <el-card class="box-card" shadow="hover">
                        <!-- <div slot="header">
                            <span class="content-type">全部用例结果</span>
                        </div> -->
                        <el-form :inline="true">
                        <el-form-item>
                            <el-input size="mini" style="width:120px;margin-left:5px;"></el-input> 
                        </el-form-item>
                        <el-form-item>
                            <el-button size="mini" type="primary">搜索</el-button>
                        </el-form-item>
                        </el-form>
                        <el-table
                        :data="this.caseList"
                        height="300px"
                        @cell-click="handle"
                        >
                            <el-table-column
                            label="测试结果"
                            prop="testapiname"
                            >
                            </el-table-column>
                        </el-table>
                    </el-card>
                </el-col>
                <el-col :span="18" style="padding-top:10px;">
                    <el-tabs type="border-card">
                        <el-tab-pane label="Sampler result">
                            <div class="input-content">
                            <p>Response headers:{{this.samplerResult.testheader}}</p>
                            <p>Status_Code:{{this.samplerResult.teststatuscode}}</p>
                            <p>Content-Type:{{this.samplerResult.testencoding}} </p>
                        </div>
                        </el-tab-pane>
                        <el-tab-pane label="Request">
                            <div class="input-content">
                                <p>RequestMethods:{{this.samplerResult.testapiMethod}}</p>
                                <p>Url:{{this.samplerResult.testurl}}</p>
                                <p>POST data:{{this.samplerResult.testapiBody}}</p>
                                <p>Request Headers:{{this.samplerResult.testheader}}</p>
                                <p>Content-Type:{{this.samplerResult.testencoding}}</p>
                                <p>Host:{{this.samplerResult.testapiHost}}</p>
                        </div>
                        </el-tab-pane >
                        <el-tab-pane label="Response data">
                            <div class="input-content">
                                <p>{{this.samplerResult.testapiResponse}}</p>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </el-col>
            </el-row>
    </div>
</template>

<script>
import reportPie from '../../components/report/reportPie'
import airPie from '../../components/report/airPie'
import reprotTable from '../../components/report/reportTable'
import bus from '../../bus'
import qs from 'qs'
export default {
    name:"reportManage",
    components:{
        reportPie,
        airPie,
        reprotTable
    },
    data(){
        return{
        activeName:"first",
        activeNames:["1"],
        reportTitle:'',
        caseList:[],
        samplerResult:[],
        taskDetail:{
            taskStartTime:'',
            taskEndTime:'',
            passRate:''
        },
        CaseData:{
                total:'',
                count_success:'',
                count_null:'',
                count_fail:''
        },
        CasesNum:[
            {
                total:0,
                count_success:0,
                count_null:0,
                count_fail:0
            }
        ],
        count:0,
        options:[],
        execuing_success:''
        }
    },
    methods:{
        //获取表格内容
        handle(row,column,event,cell){
            this.samplerResult=row
            console.log('当前行',this.samplerResult)
        },
        //查询用例开始和结束时间
        selectTestcaseTime(val){
            this.$axios.post(this.$api.selectCaseTime,qs.stringify({executing_name:val}))
            .then(response=>{
                console.log('time',response.data)
                this.taskDetail.taskStartTime=response.data[0].executing_starttime
                this.taskDetail.taskEndTime=response.data.slice(-1)[0].executing_starttime
            })
        },
        //总计
        getExecutingSuccess(val){
            console.log(val)
            bus.$emit('ExecutingSuccess',val)
        },
        //执行成功
        //未执行
        //执行失败
        //获取测试用例详情
        getApiTestCases(val){
            this.$axios.post(this.$api.selectReprotTestcase,qs.stringify({executing_name:val}))
            .then(response=>{
                console.log('获取接口用例详情',response.data)
                this.caseList=response.data.testCaseList
                //taskDetail
                bus.$emit('caseTable',response.data.testCaseList)
                this.CaseData.total=response.data.testCaseNums[0].total
                this.CaseData.count_success=response.data.testCaseNums[0].count_success
                this.CaseData.count_null=response.data.testCaseNums[0].count_null
                this.CaseData.count_fail=response.data.testCaseNums[0].count_fail
                this.CasesNum=response.data.testCaseNums
                this.taskDetail.passRate=((Number(this.CasesNum[0].count_success)/this.CasesNum[0].total)*100).toFixed(1)
                bus.$emit('airPieData',{task:this.$route.query,taskData:this.CasesNum})
                bus.$emit('reportpie',{task:this.$route.query,taskData:this.CasesNum})
            })
        },
        getRouter(){
            this.reportTitle=this.$route.query.label
            this.getApiTestCases(this.$route.query.label)
            this.selectTestcaseTime(this.$route.query.label)
            console.log('casenum-2',this.CasesNum)
        }
    },
    mounted(){
        this.getRouter()
    },
    watch:{
        '$route.query':function(){
            if('value' in this.$route.query){
                this.getRouter()
            }
        }
    },
    created(){
        this.getRouter()

    }
}
</script>
<style scoped>
.input-content{
    border:1px solid rgb(234, 234, 234);
    background-color: #EBEEF5;
    font-size: 10px;
    height: 300px;
}
.el-form-item{
    margin-bottom:0px;
}
/* .clearfix{
    background-color: #EBEEF5;
} */
.content-type{
    font-size:14px;
}
.el-card__header{
    background-color: #fafafa;
}
.text-content{
    flex:1;
    font-size:14px;
    line-height:20px;
}
.text-type{
    float: left;
}
.task-type{
    background-color: #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    margin-left: 2px;
    margin-right: 2px;
    margin-top: 2px;
    margin-bottom: 2px;
}
.report-top{
    background-color:#f5f5f5;
    height: 60px;
    width: 100%;
    text-align: left;
    padding-top: 10px;
    margin-left: 2px;
    margin-right: 5px;

}
.font-style-time{
    color: #f5f5f5;
    font-size: 10px;
    font-weight: 400;
}
.font-style{
    color: #f5f5f5;
    font-size: 20px;
    font-weight: 700;
}
.report-header{
    background-color: #09f;
    height: 60px;
    width: 100%;
    text-align: center;
    padding-top: 10px;
    margin-left: 2px;
    margin-right: 5px;
}
.font-size-title{
    font:18px large;
}
.font-size-type{
    font: 14px Base;
}
.pie-classtype{
    float: left;
    width:24%;
    height: 200px;
}
.reportType{
    background-color: #f5f5f5;
}
.grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .el-col-type{
      margin-left:10px;
      margin-top:5px;
      padding: 10px;
      /* background-color: #f5f5f5; */
  }
.text {
    font-size: 14px;
  }
.el-card__body{
    padding:1px;
    height: 250px
}
.box-card-two{
    float: left;
    width:300px;
    height: 300px;    
    margin-left: 20px;
}
  .box-card {

    width: 250px;
    height: 350px;
    /* padding-left: 20px; */
  }
  .box-card-three{
    padding-top: 5px;
    width:250px;
    margin-left: 60px;
  }
</style>


