<template>
    <el-dialog
    title="新增cookies"
    :visible.sync="addCookiesConfig"
    width="30%"
    >
        <el-form size="mini" :inline="true" class="addcookies-type">
            <el-form-item label="Name">
                <el-input style="width:300px" v-model="cookies.cookie_name" ></el-input>
            </el-form-item>
            <el-form-item label="Value">
                <el-input style="width:300px" type="textarea" rows="6" v-model="cookies.cookie_value"></el-input>
            </el-form-item>
            <el-form-item label="main">
                <el-input style="width:300px" v-model="cookies.cookie_domain"></el-input>
            </el-form-item>
            <el-form-item label="Path">
                <el-input style="width:300px" v-model="cookies.cookie_path"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
                <el-button size="mini" @click="addCookiesConfig = false">取 消</el-button>
                <el-button size="mini" type="primary" @click="addCookiesList">确 定</el-button>
        </span>
    </el-dialog>
</template>
<style>
.addcookies-type{
    padding: 3px;
}
</style>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
            addCookiesConfig:false,
            freshDialog:true,
            cookies:{
                cookie_id:null,
                cookie_name:null,
                cookie_value:null,
                cookie_domain:null,
                cookie_path:null
            }
        }
    },
    methods:{
        addCookiesList(){
            this.$axios.post(this.$api.saveCookiesConfig,qs.stringify(this.cookies))
            .then(res=>{
                if(res.data.code==200){
                    this.$message({
                        showClose:true,
                        message:res.data.msg,
                        type:'success'
                    })
                    this.addCookiesConfig=false
                    bus.$emit('freshCookiesDialog',this.freshDialog)
                }else{
                    this.$message({
                        showClose:true,
                        message:'fail',
                        type:'error'
                    })
                }
            })
        }
    },
    created(){
        bus.$on('addCookie',msg=>{
            this.addCookiesConfig=true
            this.cookies={
                cookie_id:null,
                cookie_name:null,
                cookie_value:null,
                cookie_domain:null,
                cookie_path:null
            }
        });
        bus.$on('editCookiesList',msg=>{
            console.log('cookies',msg)
            this.addCookiesConfig=true
            this.cookies.cookie_id=msg[0].cookie_id
            this.cookies.cookie_name=msg[0].cookie_name
            this.cookies.cookie_value=msg[0].cookie_value
            this.cookies.cookie_domain=msg[0].cookie_domain
            this.cookies.cookie_path=msg[0].cookie_path
        })
    }
}
</script>


