<template>
    <el-dialog
    title="免密配置"
    :visible.sync="freeDialog"
    width="50%"
    >
    <el-form :inline="true">
        <el-form-item label="版本" >
            <el-input></el-input>
        </el-form-item>
        <el-form-item label="接口名称">
            <el-input></el-input>
        </el-form-item>
        <el-form-item label="HOST">
            <el-input></el-input>
        </el-form-item>
        <el-form-item label="请求地址">
            <el-input></el-input>
        </el-form-item>
        <el-form-item label="URL地址">
            <el-input></el-input>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="dialogVisible = false">取消</el-button>
        <el-button size="mini" type="primary"  @click="dialogVisible = false">保存</el-button>
    </span>
    </el-dialog>
</template>
<script>
import bus from '../../bus'
export default {
    data(){
        return{
            freeDialog:false,
        }
    },
    methods:{

    },
    created(){
        bus.$on('openPassWordFree',msg=>{
            this.freeDialog=true
        })
    }
}
</script>


