<template>
    <div>
        <el-dialog
        title="版本配置"
        :visible.sync="dialogVisible"
        width="30%">
        <el-form :inline="true">
            <el-form-item label="版本名称">
                <el-input v-model="dialogData.modeldata_name" label-width="60px" size="mini">
                </el-input>
            </el-form-item>
            <el-form>
                <el-form-item label="所属版本">
                    <el-select style="width:120px;" v-model="dialogData.modeldata_id_id" placeholder="请选择">
                            <el-option
                                    v-for="item in dialogVersion"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                </el-form-item>
            </el-form>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="close" size="mini">取 消</el-button>
            <el-button type="primary" @click="saveModelVersion" size="mini">确 定</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<style>
.el-dialog__header{
    padding-top:2px;
    padding-right: 10px;
    padding-bottom: 2px;
    padding-left: 10px;
    background-color: #f5f7fa;
    border-radius: 5px;    
}
</style>
<script>
import bus from '../bus'
import qs from 'qs'
export default {
    props:{
        openlog:{
            type:Boolean,
            default:false,
        },
        // freshDialog:{
        //     type:Boolean,
        //     default:false,
        // }
    },
    data(){
        return{
            dialogVisible:false,
            freshDialog:false,
            dialogVersion:[],
            dialogData:{
                Modelversion_id:'',
                modeldata_name:'',
                modeldata_id_id:''
            }
        }
    },
    methods:{
        //删除单个版本
        deleteSingleVersion(val){
            this.$axios.post(this.$api.deletaSingleVersion,qs.stringify({Modelversion_id:val}))
            .then(response=>{
                if(response.data.code==0){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                }else{
                    this.$message({
                        showClose:true,
                        message:'删除失败',
                        type:'fail'
                    })
                }
            })
        },
        //搜索单个版本
        selectSigleVersion(val){
            this.$axios.post(this.$api.selectSingleVersion,qs.stringify({modelVersion:val}))
            .then(response=>{
                if('0' in response.data){
                    this.dialogData.Modelversion_id=response.data[0].Modelversion_id
                    this.dialogData.modeldata_name=response.data[0].modeldata_name
                    this.dialogData.modeldata_id_id=response.data[0].modeldata_id_id
                }else{
                    this.dialogData=[]
                }
                // console.log('查询单个版本',response.data)
                console.log('dialogData',this.dialogData)
            })
        },
        selectData(){
            this.$axios.post(this.$api.selectModelVersion)
            .then(response=>{
                // console.log('获取version信息',response.data)
                this.dialogVersion=response.data
            })
        },
        clearModelVersion(){
            this.dialogData.modeldata_name=''
            this.dialogData.modeldata_id_id=''
        },
        saveModelVersion(){
            this.$axios.post(this.$api.addVersion,qs.stringify(this.dialogData))
            .then(response=>{
                console.log(response.data)
            })
            console.log("刷新之前Dialog",this.freshDialog)
            this.dialogVisible=false
            this.freshDialog=!this.freshDialog
            bus.$emit("freshDialog",this.freshDialog)
            console.log('刷新Dialog',this.freshDialog)
        },
        close(){
            this.dialogVisible=false
            this.$emit('dialogVisible')//通知父组件更新值
            this.$emit('openlog')
        },
    },
    watch:{
        openlog: function(val,oldVal) {
            this.dialogVisible=true
        }
    },
    created(){
        this.selectData();
        bus.$on('tableData',msg=>{
            console.log('dialog获取的',msg)
            this.selectSigleVersion(msg)
        });
        bus.$on('testCaseList',msg=>{
            console.log('dialog获取的',msg)
            this.selectSigleVersion(msg)
        });
        bus.$on('addVersion',msg=>{
            this.dialogVisible=true
            this.dialogData={
                Modelversion_id:'',
                modeldata_name:'',
                modeldata_id_id:''
            }
        });
        bus.$on('editVersion',msg=>{
            if(this.dialogData.Modelversion_id==''){
                this.$message({
                    showClose:true,
                    message:'请选择一个版本',
                    type:'info'
                })
            }else{
                this.dialogVisible=true
            }
        });
        bus.$on('delVersion',msg=>{
            if(this.dialogData.Modelversion_id==''){
                this.$message({
                    showClose:true,
                    message:'请选择一个版本',
                    type:'info'
                })
            }else{
                this.deleteSingleVersion(this.dialogData.Modelversion_id)
            }
        });
}
};
</script>



