<template>
    <div>
        <el-dialog
        :visible.sync="dialogVisible"
        width="31%"
        >
        <el-form>
            <el-form-item label="关键字">
                <el-input v-model="keyList.keyData" size="mini"></el-input>
            </el-form-item>
        </el-form>
         <span slot="footer" class="dialog-footer">
            <el-button size="mini" @click="dialogVisible=false">取 消</el-button>
            <el-button size="mini" type="primary" @click="saveKey">确 定</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
            dialogVisible:false,
            keyList:{
                key_di:'',
                keyData:''
            }
        }
    },
    methods:{
        saveKey(){
            this.$axios.post(this.$api.saveWord,qs.stringify(this.keyList))
            .then(response=>{
                if(response.data.code==200){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:"success"
                    })
                }else{
                    this.$message({
                        showClose:true,
                        message:"新增关键字失败",
                        type:"fail"
                    })
                }
                this.dialogVisible=false
            })
        }
    },
    created(){
        bus.$on('addKeyWords',msg=>{
            this.dialogVisible=true
        })
    }
}
</script>


