<template>
    <div>
        <el-container>
    <el-header style="height: 30px;">
      <el-dropdown style="float:right;line-height:20px;top:6px;color: rgb(255, 255, 255);">
        <i class="el-icon-setting el-icon--right" style="margin-right: 15px"></i>
        <span>{{this.userName}}</span>
        <el-dropdown-menu>
          <el-dropdown-item>查看个人信息</el-dropdown-item>
          <el-dropdown-item @click.native="userLogoOut">注销登录</el-dropdown-item>
          <!-- <el-dropdown-item>删除</el-dropdown-item> -->
        </el-dropdown-menu>
      </el-dropdown>
      
    </el-header>
        </el-container>
    </div>
</template>
<script>
import bus from '../bus'
export default {
  data(){
    return{
      userName:''
    }
  },
  methods:{
    //注销登录
    userLogoOut(){
      this.$axios.get(this.$api.userLoginOut)
      .then(response=>{
                if(response.data.code==200){
                    this.$message({
                        showClose:true,
                        message:response.data.msg,
                        type:'success'
                    })
                    this.$router.push({path:'/'});
                    this.$cookies.remove('token');
                }else{
                    this.$message({
                        showClose:true,
                        message:'注销失败',
                        type:'fail'
                    })
                }
            })
    },
    //查看个人信息
    getUserInfo(){
      this.$axios.get(this.$api.userInfo)
      .then(response=>{
        this.userName=response.data
      })
    }
  },
  created(){
    this.getUserInfo()
  }
}
</script>
<style>
    .el-header {
        background-color: #434650;
        color: #434650;

        line-height: 60px;
    }
</style>


